import React, { useEffect, useState } from "react";
import { Card, Form } from "antd";
import { Qtable } from "common";
import { GoodColumns, handleLogColumns, giftSkuColumns } from "../columns";
import StoreOutInfo from "./components/StoreOutInfo";
import { getInfoApi } from "api/home/OrderCenter/Corder/UserOrder";

/**
 * 
 * 
 */
const UserOrderInfo = (props) => {
  const [orderInfo, setOrderInfo] = useState({});
  const [userInfo, setUserInfo] = useState({});
  const [skuList, setSkuList] = useState([]);
  const [spShopInfo, setSpShopInfo] = useState({});
  const [orderPackageList, setOrderPackageList] = useState([]);
  const [orderOperateLogList, setOrderOperateLogList] = useState([]);
  const [giftSkuList, setGiftSkuList] = useState([]);
  const { id } = props.match.params;
  useEffect(() => {
    getInfoApi({ orderNo: id }).then((res) => {
      if (res.httpCode == 200) {
        const {
          orderInfo,
          userInfo,
          skuList,
          spShopInfo,
          orderPackageList,
          orderOperateLogList,
          giftSkuList,
        } = res.result;
        setOrderInfo(orderInfo);
        setUserInfo(userInfo);
        setSkuList(skuList);
        setSpShopInfo(spShopInfo);
        setOrderPackageList(orderPackageList);
        setOrderOperateLogList(orderOperateLogList);
        setGiftSkuList(giftSkuList);
      }
    });
  }, []);
  return (
    <div>
      <Card title="" className="base_info">
        <Form.Item label="">{orderInfo.orderNo}</Form.Item>
        <Form.Item label="">{orderInfo.virtualOrderNo}</Form.Item>
        <Form.Item label="">{orderInfo.createTimeStr}</Form.Item>
        <Form.Item label="">{orderInfo.orderStatusStr}</Form.Item>
        <Form.Item label="">{orderInfo.flowStatusStr}</Form.Item>
        <Form.Item label="">{orderInfo.orderNum}</Form.Item>
        <Form.Item label="">{orderInfo.platformStr}</Form.Item>
        <Form.Item label="">{orderInfo.orderTypeStr}</Form.Item>
        <Form.Item label="">{orderInfo.deliveryTypeStr}</Form.Item>
        <Form.Item label="">{orderInfo.orderAmount}</Form.Item>
        <Form.Item label="">{orderInfo.amountSum}</Form.Item>
        <Form.Item label="">
          {orderInfo.standardExpressAmount}
        </Form.Item>
        <Form.Item label="">{orderInfo.discountAmount}</Form.Item>
        <Form.Item label="">{orderInfo.couponAmount}</Form.Item>
        <Form.Item label="">{orderInfo.couponCode}</Form.Item>
        <Form.Item label="">{orderInfo.appVersion}</Form.Item>
      </Card>

      <Card title="" className="base_info">
        <Form.Item label="">{userInfo.nickname}</Form.Item>
        <Form.Item label="">{userInfo.mobilePhone}</Form.Item>
        <Form.Item label="">{userInfo.userMemberLevelStr}</Form.Item>
        <Form.Item label="">{userInfo.orderUserNum}</Form.Item>
        <Form.Item label="">{userInfo.orderShopNum}</Form.Item>
      </Card>

      <Card title="">
        <Qtable columns={GoodColumns} dataSource={skuList} />
        <div style={{ color: "red" }}>()</div>
        <Qtable columns={giftSkuColumns} dataSource={giftSkuList} />
      </Card>

      <Card title="" className="base_info">
        <Form.Item label="">{spShopInfo.reOrderNo}</Form.Item>
        <Form.Item label="">{spShopInfo.channelOrderNo}</Form.Item>
        <Form.Item label="">{spShopInfo.deliveryTypeStr}</Form.Item>
        <Form.Item label="">{spShopInfo.phone}</Form.Item>
      </Card>

      <Card title="">
        <StoreOutInfo orderPackageList={orderPackageList} />
      </Card>

      <Card title="">
        <Qtable columns={handleLogColumns} dataSource={orderOperateLogList} />
      </Card>
    </div>
  );
};
export default UserOrderInfo;
