import React from "react";
import TableDataListUtil from "utils/TableDataListUtil";
import { Spin } from "antd";

/**
 *
 * @param ChildComponent Dom
 * @param apiRequest function
 * @param isComponentDidMountRequestData 
 * @param dataListOptionsKey  keyindex
 * @param childStateParams 
 * @param formatSearchCriteriaList 
 * @param onModalCancelClick modal
 * @param optionsResponseDataFun 
 */
function QbaseList(
  ChildComponent,
  apiRequest,
  isComponentDidMountRequestData,
  dataListOptionsKey = null,
  childStateParams = null,
  formatSearchCriteriaList = null,
  onModalCancelClick = null,
  optionsResponseDataFun = null
) {
  return class extends React.Component {
    state = {
      /**
       * 
       */
      dataList: [],
      /**
       * 
       */
      everyPage: 15,
      /**
       * 
       */
      currentPage: 1,
      /**
       * 
       */
      total: 0,
      /**
       * 
       */
      searchCriteriaList: {},
      /**
       * ,
       */
      recordSearchCriteriaList: {},
      /**
       * key
       */
      selectedRowKeys: [],
      /**
       * 
       */
      selectedRows: [],
      /**
       * Modal
       */
      showModal: false,
      /**
       * modalkey
       */
      showModalKey: "",
      /**
       * 
       */
      showLoadingStatus: false,
      /**
       * 
       */
      ...childStateParams,
    };

    /**
     * 
     */
    componentDidMount = () => {
      if (isComponentDidMountRequestData) {
        this.searchDataList();
      }
    };

    /**
     * 
     * @param selectedRowKeys
     * @param selectedRows
     */
    rowSelectChange = (selectedRowKeys, selectedRows) => {
      this.setState({
        selectedRowKeys,
        selectedRows,
      });
    };

    /**
     * 
     * @param currentPage 
     * @param everyPage 
     */
    changePage = (currentPage, everyPage) => {
      this.searchDataList(
        this.state.searchCriteriaList,
        false,
        currentPage,
        everyPage
      );
    };

    /**
     * selection
     */
    getTableRowSelection(type) {
      const { selectedRowKeys } = this.state;
      const rows = {
        selectedRowKeys,
        ...type,
      };
      return {
        onChange: this.rowSelectChange,
        ...rows,
      };
    }

    /**
     * 
     * @param values 
     * @param isUseRecord 
     * @param currentPage 
     * @param everyPage 
     */
    searchDataList = (
      values,
      isUseRecord = true,
      currentPage = null,
      everyPage = null
    ) => {
      this.showLoading();
      //
      let params = isUseRecord
        ? {
            ...this.state.searchCriteriaList,
            ...this.state.recordSearchCriteriaList,
          }
        : { ...this.state.searchCriteriaList };
      if (formatSearchCriteriaList != null) {
        params = { ...params, ...formatSearchCriteriaList(this, values) };
      } else {
        params = { ...params, ...values };
      }
      apiRequest({ ...params, currentPage, everyPage }, this)
        .then((res) => {
          this.hideLoading();
          const { result, everyPage, total, currentPage } =
            res.result != null ? res.result : res;
          this.setState(
            {
              dataList: TableDataListUtil.addKeyAndResultList(
                result,
                dataListOptionsKey
              ),
              everyPage,
              total: total,
              currentPage,
              searchCriteriaList: params,
              selectedRowKeys: [],
            },
            optionsResponseDataFun != null
              ? function () {
                  //
                  optionsResponseDataFun(this, res);
                }
              : null
          );
        })
        .catch(() => {
          this.hideLoading();
        });
    };

    /**
     * 
     */
    refreshDataList = () => {
      //
      onModalCancelClick != null
        ? onModalCancelClick(this)
        : this.setState({ showModal: false });
      //
      let currentPage = this.state.currentPage;
      let everyPage = this.state.everyPage;
      this.searchDataList({
        ...this.state.searchCriteriaList,
        currentPage,
        everyPage,
      });
    };

    /**
     * 
     * @param values 
     * @param isDefaultInitFinish 
     */
    selectTimeChange = (values, isDefaultInitFinish) => {
      const params = { ...this.state.searchCriteriaList, ...values };
      this.setState({ recordSearchCriteriaList: params });
      if (isDefaultInitFinish) {
        this.searchDataList(params);
      }
    };

    /**
     * 
     */
    showLoading() {
      this.setState({
        showLoadingStatus: true,
      });
    }

    /**
     * 
     */
    hideLoading() {
      this.setState({
        showLoadingStatus: false,
      });
    }

    render() {
      return (
        <Spin spinning={this.state.showLoadingStatus}>
          {ChildComponent(this)}
        </Spin>
      );
    }
  };
}

export default QbaseList;
