import {
  Input,Spin,Form,Upload,Select,Table,Card,Tag,
  Row,Col,Checkbox,Button,Radio,AutoComplete,DatePicker
} from 'antd';
import { Qbtn, Qmessage, BaseEditTable, QupLoadAndDownLoad } from 'common';
import { useState, useEffect } from 'react';
import Proration from '../components/Proration';
import { GetBrandApi, GetAddApi, GetSpuCodeApi } from 'api/marketCenter/CouponCenter';
import { GetSuppliApi } from 'api/marketCenter/CtipActivity';
import { ColumnsAdd } from './columns';

let { RangePicker } =DatePicker;
let TextArea = Input.TextArea;
let FormItem = Form.Item;
let Option = Select.Option;
let RadioGroup = Radio.Group;

const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 4 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 20 },
      },
    };
const CouponAdd=({...props})=> {
  const [form] = Form.useForm();
  let [totalData, setTotalData]=useState({couponUseScope:"4"});
  let [goodsList, setGoodsList] =useState([]);//
  let [tagsList, setTagsList] =useState([]);//
  let [ratioList, setRatioList] =useState([]);//
  let [brandList, setBrandList] =useState([]);//list
  let [selectBdList, setSelectBdList] =useState([]);//
  let [selectBdKeyList, setSelectBdkeyList] =useState([]);
  let [limitDisabled, setLimitDisabled] =useState(false);

  //
  const upDateFileList=(response)=> {
    let { result } = response;
    result=result?result:[]
    result.map((el,index)=>el.key=index)
    setGoodsList(result)
  }
  const upDateGoodsList=(array)=> {
    setGoodsList(array)
  }
  const upDateRatioList=(array)=> {
    setRatioList(array)
  }
  const upDateTagList=(array)=> {
    setTagsList(array)
  }
  const handleChangeType=(e)=> {
    let value = e.target.value;
    switch(value) {
      case 1:
      case 2:
        setLimitDisabled(true);
        break;
      case 3:
      case 4:
        setLimitDisabled(false);
        break;
    }
  }
  // 
  const handleBrandSearch = value => {
    if(!value){
      return;
    }
    GetBrandApi({ brandName: value })
    .then(res => {
      let { result }=res;
      result = result?result:[];
      result = result.map((el)=> {
        let item={};
        item.key = el.id;
        item.value = el.brandNameCn;
        return item;
      })
      setBrandList(result);
    });
  };
  //
  const handleBrandSelect = (value, option) => {
    selectBdList = [...selectBdList,...[{key:option.key,value:value}]];
    let bdKeyList = selectBdList.map((el)=>{return el.key})
    setSelectBdList(selectBdList);
    setSelectBdKeyList(bdKeyList);
  };
  //
  const handleBlur=(e,index)=> {
    let value = e.target.value;
    let params = {
      pdCode:value,
      couponUseScope:totalData.couponUseScope,
      pdBrandIdList:selectBdKeyList
    }
    GetSpuCodeApi(params)
    .then((res)=> {
      let { pdList } =res.result;
      goodsList[index]={...pdList,key:index};
      goodsList=[...goodsList]
      setGoodsList(goodsList)
    })
  }
  const submit = async () => {
    try {
      let values = await form.validateFields();
      let { bearers, costApportion, ..._val} =values;
      let proportionList = ratioList.map((el)=> {
        return {
          bearer:el.bearer,
          proportion:el.proportion,
          bearerName:el.bearerStr,
        }
      })
      _val = {..._val,proportionList}
      console.log(_val);
      GetAddApi(_val)
      .then((res)=> {
        Qmessage.success('')
        goReturn()
      })
    } catch (errorInfo) {
      console.log('Failed:', errorInfo);
    }
  }
  const goReturn=()=> {
    props.history.push('/account/coupon_centre')
  }
  //change
  const onValuesChange=(changedValues, allValues)=> {
    let currentKey = Object.keys(changedValues)[0];
    if(currentKey == 'bearers') {
      let newArray = [...allValues['bearers']];
      setRatioList(newArray);
    }
    if(currentKey=='list') {
      return;
    }
    totalData = {...totalData,...allValues};
    setTotalData(totalData)
  }
  const tipsMap=(type)=> {
    let tips;
    switch(type) {
      case 1:
        tips="";
        break;
      case 3:
        tips="App";
        break;
      case 2:
        tips="";
        break;
      case 4:
        tips="";
        break;
      default:
        tips="";
    }
    return tips;
  }
  useEffect(()=>{ form.setFieldsValue({bearers:ratioList}) },[ratioList]);
  useEffect(()=>{ form.setFieldsValue(totalData) },[totalData]);
  useEffect(()=>{ form.setFieldsValue({list:goodsList}) },[goodsList]);
  console.log(ratioList)
  return(
    <Spin tip="..." spinning={false}>
      <div className="oms-common-addEdit-pages ctipActivity-addEdit-pages">
        <Form
          onValuesChange={onValuesChange}
          className="common-addEdit-form"
          form={form}
          {...formItemLayout}>
          <Card title="">
            <FormItem
              label="">
              <FormItem
                name="couponName"
                rules={[{ required: true, message: "" }]} noStyle>
                <Input
                  placeholder="12"
                  maxLength="12"
                  autoComplete="off"/>
              </FormItem>
                
            </FormItem>
            <FormItem label="">
              <FormItem
                name="couponExplain"
                rules={[{ required: true, message: "" }]} noStyle>
                <TextArea
                  placeholder="50"
                  maxLength="50"
                  rows={6}/>
              </FormItem>
              
            </FormItem>
            <FormItem
              label=""
              name="platform"
              rules={[{ required: true, message: "" }]}>
              <RadioGroup>
                <Radio value="1"></Radio>
                <Radio value="2"></Radio>
              </RadioGroup>
            </FormItem>
            <FormItem
              label="">
              <FormItem
                noStyle
                name="couponMoney"
                rules={[ { required: true, message: "" },{ pattern: /^(?:[0-9]{0,4}|10000)$/, message: "0-10000"}]}>
                <Input
                  placeholder=""
                  autoComplete="off"/>
              </FormItem>
                
            </FormItem>
            <FormItem label="">
              <FormItem
                noStyle
                name="couponFullAmount"
                rules={[{ required: true, message: "" },{ pattern: /^([1-9]\d*|[0]{1,1})$/, message: "" }]}>
                <Input autoComplete="off" />
              </FormItem>
              ,0
            </FormItem>
            <Proration
              supplierApi={GetSuppliApi}
              upDateRatioList={upDateRatioList}
              upDateTagList={upDateTagList}
              tagsList={tagsList}
              ratioList={ratioList}
              form={form}/>
            <FormItem label="">
              <FormItem
                name="couponValid"
                rules={[{ required: true, message: "" }]}>
                <RadioGroup>
                  <Radio  value={1}> </Radio>
                  <Radio  value={2}>  </Radio>
                </RadioGroup>
              </FormItem>
              <FormItem
                noStyle
                shouldUpdate={(prevValues, currentValues) => prevValues.couponValid !== currentValues.couponValid}>
                {({ getFieldValue }) => {
                  return getFieldValue('couponValid')&&(getFieldValue('couponValid') === 1 ?
                    <FormItem noStyle>
                      <FormItem
                        noStyle
                        name="couponValidDay"
                        rules={[{ required: true, message: ""}]}>
                        <Input autoComplete="off"/>
                      </FormItem>
                      0
                    </FormItem>
                    :
                    <FormItem name="couponValidDate" rules={[{ required: true, message: "" }]}>
                      <RangePicker
                        showTime
                        format="YYYY-MM-DD HH:mm:ss"/>
                    </FormItem>)
                }}
              </FormItem>
            </FormItem>
            <FormItem label="">
              <FormItem
                name="couponUseScene"
                rules={[{ required: true, message: "" }]}>
                <RadioGroup onChange={handleChangeType}>
                  <Radio value={1}></Radio>
                  <Radio value={3}></Radio>
                  <Radio value={2}></Radio>
                  <Radio value={4}></Radio>
                </RadioGroup>
              </FormItem>
              <div className="suffix_tips coupon_tips">
                <p>{tipsMap(totalData.couponUseScene)}</p>
              </div>
            </FormItem>
            <FormItem label="">
              <FormItem
                name="isSend"
                rules={[{ required: true, message: "" }]}>
                <RadioGroup>
                  <Radio  value={-1} disabled={limitDisabled}>  </Radio>
                  <Radio value={1}> </Radio>
                </RadioGroup>
              </FormItem>
              <FormItem
                noStyle
                shouldUpdate={(prevValues, currentValues) => prevValues.isSend !== currentValues.isSend}>
                {({ getFieldValue }) => {
                  return getFieldValue('isSend')&&getFieldValue('isSend')==1&&
                    <FormItem noStyle>
                      <FormItem
                        name="couponCount"
                        rules={[
                          { required: true, message: "" },
                          { pattern: /^(?:[0-9]{0,4}|10000)$/, message: "0-10000"}
                        ]}>
                        <Input
                          placeholder="0-10000"
                          autoComplete="off"/>
                      </FormItem>
                      
                      <FormItem noStyle>
                        
                      </FormItem>
                    </FormItem>
                }}
              </FormItem>
            </FormItem>
            <FormItem label="">
              <FormItem
                name="couponRemark">
                <TextArea
                  placeholder="255"
                  maxLength="255"
                  rows={6}/>
              </FormItem>
              <span className='suffix_tips'></span>
            </FormItem>
          </Card>
          <Card title="">
            <FormItem
              label=""
              name="couponUseScope"
              rules={[{ required: true, message: "" }]}>
              <RadioGroup>
                <Radio value="4"></Radio>
                <Radio value="1"></Radio>
                <Radio value="2"></Radio>
                <Radio value="5"></Radio>
              </RadioGroup>
            </FormItem>
            <FormItem
              noStyle
              shouldUpdate={(prevValues, currentValues) => prevValues.couponUseScope !== currentValues.couponUseScope}>
              {({ getFieldValue }) => {
                return getFieldValue('couponUseScope') == "5"&&
                  <FormItem label="">
                    <FormItem
                      name="brand"
                      rules={[{ required: true, message: ""}]}>
                      <AutoComplete
                        options={brandList}
                        onSelect={handleBrandSelect}
                        onSearch={handleBrandSearch}/>

                    </FormItem>
                    {selectBdList.map((el)=> (
                      <Tag
                        closable
                        key={el.key}
                        onClose={()=>handleClose(el)}>
                        {el.value}
                      </Tag>
                    ))}
                  </FormItem>
              }}
            </FormItem>
            <FormItem
              label=""
              name="spuScope"
              rules={[{ required: true, message: "" }]}>
              <RadioGroup>
                <Radio value={0}></Radio>
                <Radio value={1}></Radio>
                <Radio value={2}></Radio>
              </RadioGroup>
            </FormItem>
            <FormItem
              noStyle
              shouldUpdate={(prevValues, currentValues) => prevValues.spuScope !== currentValues.spuScope}>
              {({ getFieldValue }) => {
                return getFieldValue('spuScope') == 1&&
                <QupLoadAndDownLoad
                  label=""
                  fileName="activityCoupon"
                  data={{
                    pdBrandIdList:selectBdKeyList,
                    couponUseScope:totalData.couponUseScope
                  }}
                  action="/qtoolsApp/couponManager/pdimport"
                  upDateList={upDateFileList}>
                    <BaseEditTable
                      btnText=""
                      upDateList={upDateGoodsList}
                      dataSource={goodsList}
                      columns={ColumnsAdd(handleBlur)}/>
                </QupLoadAndDownLoad>
              }}
            </FormItem>
          </Card>
          <div className="handle-operate-save-action">
            <Qbtn onClick={goReturn}>  </Qbtn>
            <Qbtn size="free" onClick={()=>submit(1)}></Qbtn>
          </div>
        </Form>
      </div>
  </Spin>
  )
}

export default CouponAdd;
