import { Qpagination, Qbtn } from "common";
import {
  GetListsApi,
  changeStatusApi,
  upStatusApi,
  downStatusApi
} from "api/home/GoodsCenter/Bgoods/GoodList";
import { parColumns, subColumns } from "./column";
import QsubTable from "common/QsubTable";
import FilterForm from "./components/FilterForm";
import { message, Modal } from "antd";

const tipsText = {
  1: "Q-",
  2: "Q-",
  3: "Q-",
  4: "Q-"
};
class Bgoods extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      everyPage: 20,
      currentPage: 0,
      total: 0,
      goodLists: [],
      visible: false,
      attr: "",
      inputValues: {},
      selectedRowKeys: []
    };
  }
  //
  componentDidMount = () => {
    this.searchData();
  };
  //
  searchData = values => {
    const params = { ...this.state.inputValues, ...values };
    GetListsApi(params).then(res => {
      if (res.httpCode == 200) {
        const { result, everyPage, total, currentPage } = res.result;
        this.setState({
          goodLists: result,
          everyPage,
          total: total,
          currentPage,
          inputValues: params
        });
      }
    });
  };
  //
  changePage = (currentPage, everyPage) => {
    this.searchData({ ...this.state.inputValues, currentPage, everyPage });
  };
  //
  onSubmit = values => {
    this.searchData(values);
  };
  //
  batchOperate = attr => {
    if (!this.state.selectedRowKeys.length) {
      message.warning("", 0.8);
      return;
    }
    this.setState({
      visible: true,
      attr
    });
  };
  //
  onOk = () => {
    const { attr, selectedRowKeys } = this.state;
    let type;
    if (attr == 1 || attr == 2) {//&
      type = "-100";
    }
    if (attr == 3 || attr == 4) {//&
      type = "-200";
    }
    if (attr == 1 || attr == 3) {//
      upStatusApi({ type, codes: selectedRowKeys }).then(res => {
        if (res.httpCode == 200) {
          this.searchData();
          this.onCancel();
        }
      });
    }
    if (attr == 2 || attr == 4) {//
      downStatusApi({ type, codes: selectedRowKeys }).then(res => {
        if (res.httpCode == 200) {
          this.searchData();
          this.onCancel();
        }
      });
    }
  };
  //
  onCancel = () => {
    this.setState({
      visible: false,
      attr: "",
      selectedRowKeys:[]
    });
  };
  //
  handleOperateClick = (record, attr) => {//attr(1:'' 0:)
    const { id } = record;
    changeStatusApi({ id, upperStatus: attr }).then(res => {
      if (res.httpCode == 200) {
        const text = attr == 1 ? "" : "";
        message.success(text, 0.8);
        this.searchData();
      }
    });
  };
  formatList = data => {
    data &&
      data.map(item => {
        item.key = item.spuCode;
        item["subList"] &&
          item["subList"].map(subItem => {
            subItem.key = subItem.id;
            let [totalStockQty, totalSaleQty] = [0, 0];
            totalStockQty += Number(subItem.stockQty);
            totalSaleQty += Number(subItem.saleQty);
            item.totalStockQty = totalStockQty;
            item.totalSaleQty = totalSaleQty;
            return subItem;
          });
        return item;
      });
    return data;
  };
  //
  onChange = selectedRowKeys => {
    console.log(selectedRowKeys);
    this.setState({
      selectedRowKeys
    });
  };
  render() {
    const {
      goodLists,
      everyPage,
      total,
      currentPage,
      attr,
      visible,
      selectedRowKeys
    } = this.state;
    const dataSource = this.formatList(goodLists);
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onChange
    };
    return (
      <div className="oms-common-index-pages-wrap">
        <FilterForm onSubmit={this.onSubmit} />
        <div className="handle-operate-btn-action">
          <Qbtn onClick={() => this.batchOperate(1)}></Qbtn>
          <Qbtn onClick={() => this.batchOperate(2)}></Qbtn>
          <Qbtn onClick={() => this.batchOperate(3)}></Qbtn>
          <Qbtn onClick={() => this.batchOperate(4)}></Qbtn>
        </div>
        {goodLists.length > 0 && (
          <QsubTable
            subColumns={subColumns}
            parColumns={parColumns}
            dataSource={dataSource}
            onOperateClick={this.handleOperateClick}
            rowSelection={rowSelection}
          />
        )}
        <Qpagination
          data={{ everyPage, currentPage, total }}
          onChange={this.changePage}
        />
        {visible && (
          <Modal
            className="modal_center"
            visible={visible}
            onOk={this.onOk}
            onCancel={this.onCancel}
            okText=""
            cancelText=""
          >
            <div className="tips"> {tipsText[attr]} </div>
          </Modal>
        )}
      </div>
    );
  }
}

export default Bgoods;
