import React from 'react'
import {Button, Modal} from "antd";
import TopTitleDesHeader from "../../components/TopTitleDesHeader";
import {GetClassifyAnalysis} from "api/home/DataCenter/BaseData/GoodsData";
import CommonUtils from "utils/CommonUtils";
import ClassifyAnalysisCharts from "../components/Charts/ClassifyAnalysisCharts";
import {ErpExportApi} from "api/Export";
import {Qpagination, Qtable} from "common/index";
import Columns from "./column";

/**
 * 
 *  2020/3/14 21:56
 * LorenWang
 * 
 * 
 * 
 * 
 * 
 */
export default class ClassifyAnalysis extends React.Component {
    state = {
        chatData: {
            xdata: [],
            type: '1',
            data1: [],
            data2: [],
            data3: [],
            data4: [],
        },
        searchFilterList: {},
        updateTime: "",
        categoryAnalysis: []
    };

    /**
     * 
     */
    desInfo = () => {
        Modal.info({
            title: '',
            content: (
                <div>
                    <p>Q</p>
                    <p> </p>
                    <p>Q</p>
                    <p></p>
                    <p>POSPOS</p>
                    <p>POSPOS </p>
                    <p>POSPOS</p>
                    <p>POSPOS</p>
                </div>
            ),
            onOk() {
            },
        });
    };

    /**
     * 
     */
    getData(values) {
        let i;
        const result = new GetClassifyAnalysis();
        const categoryAnalysis = result.categoryAnalysis;
        const updateTime = result.updateTime;
        for (i = 0; i < categoryAnalysis.length; i++) {
            categoryAnalysis[i].qbyQtyBi = CommonUtils.dataDifferenceValueComparison(categoryAnalysis[i].qbyQty, categoryAnalysis[i].ofQbyQty, false) + '%'; //
            categoryAnalysis[i].qbyAmountBi = CommonUtils.dataDifferenceValueComparison(categoryAnalysis[i].qbyAmount, categoryAnalysis[i].ofQbyAmount, false) + '%'; //
            categoryAnalysis[i].posQtyBi = CommonUtils.dataDifferenceValueComparison(categoryAnalysis[i].posQty, categoryAnalysis[i].ofPosQty, false) + '%'; //pos
            categoryAnalysis[i].posAmountBi = CommonUtils.dataDifferenceValueComparison(categoryAnalysis[i].posAmount, categoryAnalysis[i].ofPosAmount, false) + '%'; //pos
        }

        const xdata = [];
        const data1 = [];
        const data2 = [];
        const data3 = [];
        const data4 = [];
        for (i = 0; i < categoryAnalysis.length; i++) {
            xdata.push(categoryAnalysis[i].name);
            data1.push(categoryAnalysis[i].qbyQty); //
            data2.push(categoryAnalysis[i].posQty); //pos
            data3.push(categoryAnalysis[i].qbyAmount);//
            data4.push(categoryAnalysis[i].posAmount); //pos
        }

        this.setState({
            chatData: {
                xdata: xdata,
                type: '1',
                data1: data1,
                data2: data2,
                data3: data3,
                data4: data4,
            },
            updateTime: updateTime,
            categoryAnalysis: categoryAnalysis
        })
    }

    /**
     * 
     * @param values 
     * @param isDefaultInitFinish 
     */
    selectTimeChange(values, isDefaultInitFinish) {
        const params = {...this.state.searchFilterList, ...values};
        //
        this.setState({searchFilterList: {...params}});
        //
        this.getData(params);
    };


    render() {
        return <div>
            <TopTitleDesHeader updateTime={this.state.updateTime}
                               desInfoClick={this.desInfo.bind(this)}/>
            <div style={{border: '1px solid #e8e8e8', padding: '20px', marginTop: '30px'}}>
                <ClassifyAnalysisCharts
                    dataInfo={this.state.chatData}
                    selectTimeChange={this.selectTimeChange.bind(this)}/>
                <Button
                    type="primary"
                    size='large'
                    style={{marginTop: '20px', marginBottom: '15px'}}
                    onClick={() => new ErpExportApi(this.state.searchFilterList)}>
                    
                </Button>
                <Qtable
                    columns={Columns}
                    dataSource={this.state.categoryAnalysis}/>
            </div>
        </div>
    }
}
