import QuploadFileList from "common/QuploadFileList";
import { Form, Table, Button, Input } from "antd";
import moment from "moment";

const Editable = props => {
  const { dataSource, changeDataSource, getPrice } = props;
  /**
   * inputlist
   * @param {*} e 
   * @param {*} record 
   * @param {*} keyName 
   */
  const changeData = (e, record, keyName) => {
    const { value } = e.target;
    if (value) {
      const newData = [...dataSource];
      const index = newData.findIndex(item => item.key == record.key);
      const obj = { ...record, [keyName]: value };
      newData.splice(index, 1, obj);
      changeDataSource(newData);
    }
  };
  const Columns = [
    {
      width: 200,
      title: "SKU",
      dataIndex: "itemCode",
      render: (text, record, index) => {
        return (
          <Form.Item
            name={["goodList", index, "itemCode"]}
            rules={[{ required: true, message: "SKU" }]}
          >
            <Input
              onBlur={e => getPrice(e, record)}
              autoComplete="off"
              placeholder="30C"
            />
          </Form.Item>
        );
      }
    },
    {
      width: 100,
      title: "",
      dataIndex: "amount",
      render: (text, record, index) => {
        return (
          <Form.Item
            name={["goodList", index, "amount"]}
            rules={[
              { required: true, message: "" },
              { pattern: /^([1-9][0-9]*){1,3}$/, message: "0" }
            ]}
          >
            <Input
              onBlur={e => changeData(e, record, "amount")}
              autoComplete="off"
              placeholder=""
            />
          </Form.Item>
        );
      }
    },
    {
      width: 100,
      title: "",
      dataIndex: "price",
      render: (text, record, index) => {
        return (
          <Form.Item
            name={["goodList", index, "price"]}
            rules={[
              { required: true, message: "" },
              { pattern: /^\d+(\.\d{0,4})?$/, message: "0" }
            ]}
          >
            <Input
              onBlur={e => changeData(e, record, "price")}
              autoComplete="off"
              placeholder=""
            />
          </Form.Item>
        );
      }
    },
    {
      width: 100,
      title: "",
      dataIndex: "procurementTarget",
      render: (text, record, index) => {
        return <span>{text == 1 ? "" : text == 2 ? "qtools" : ""}</span>;
      }
    },
    {
      width: 100,
      title: "",
      dataIndex: "salesAttributeName"
    },
    {
      width: 100,
      title: "SKU",
      dataIndex: "createTime",
      render: (text, record, index) => {
        return (
          <span>{text && moment(text).format("YYYY-MM-DD HH:mm:ss")}</span>
        );
      }
    }
  ];
  const downLoadTemp = () => {
    window.open("src/static/purchase_in.xlsx");
  };
  return (
    <div>
      <QuploadFileList
        action="/qtoolsOms/upload/file_excel_return_list"
        dataSource={dataSource}
        Columns={Columns}
        changeDataSource={changeDataSource}
        downLoadTemp={downLoadTemp}
        footer={true}
        del={true}
      />
    </div>
  );
};

export default Editable;
