import { useEffect } from "react";
import { Modal, Form, Input, Select } from "antd";
const FormItem = Form.Item;
import {GetInfoApi} from 'api/home/GoodsCenter/BaseConfig/CgoodsExplain'
const Option = Select.Option;
const TextArea = Input.TextArea;

const ExplainModal=(props)=> {
  const [form] = Form.useForm()
  const {pdExplainId,onOk,onCancel,visible} = props;
  useEffect(()=>{
    if(pdExplainId){
      GetInfoApi({id:pdExplainId}).then(res=>{
        if(res.httpCode == 200){
          form.setFieldsValue(res.result)
        };
      })
    }
  },[])
  const clearForm = () => {
    form.resetFields();
  };
  const ok = async() => {
    const values = await form.validateFields();
    values.name = values.name.trim();
    if(pdExplainId){
      values.pdExplainId = pdExplainId
    }
    onOk(values, clearForm);
  };
  const cancel = () => {
    onCancel(clearForm);
  };
    return (
      <div>
        <Modal
          title={pdExplainId?'':''}
          visible={visible}
          onOk={ok}
          onCancel={cancel}
        >
          <Form form={form}>
            <FormItem
              label=""
              name="name"
              rules={[
                { required: true, message: "15" }
              ]}
              labelCol={{ span: 5 }}
              wrapperCol={{ span: 12 }}
            >
              <Input
                maxLength="15"
                placeholder="15"
                autoComplete="off"
              />
            </FormItem>
            <FormItem
              label=""
              labelCol={{ span: 5 }}
              wrapperCol={{ span: 12 }}
              name="text"
              rules={[{ required: true, message: "100" }]}
            >
              <TextArea
                maxLength="100"
                placeholder="100"
                autoComplete="off"
              />
            </FormItem>
            <FormItem
              label=""
              labelCol={{ span: 5 }}
              wrapperCol={{ span: 12 }}
              name="rank"
              rules={[
                {
                  pattern: /^(?:[0-9]{0,2}|100)$/,
                  message: "0-100"
                },
                {
                  required: true,
                  message: "0-100"
                }
              ]}
            >
              <Input
                maxLength="15"
                placeholder="0-100"
                autoComplete="off"
              />
            </FormItem>
            <FormItem
              label=""
              labelCol={{ span: 5 }}
              wrapperCol={{ span: 12 }}
              name="status"
              rules={[{ required: true, message: "" }]}
            >
              <Select
                allowClear={true}
                placeholder=""
                className="select"
              >
                <Option value={1}></Option>
                <Option value={0}></Option>
              </Select>
            </FormItem>
          </Form>
        </Modal>
      </div>
    );
}
export default ExplainModal;
