import { Modal, Form, Select, Input } from "antd";
import React, { useState, useEffect } from "react";
import { getExpressListApi } from "api/home/OrderCenter/BondedOrder";
const formLayout = {
  labelCol:{span:6},
  wrapperCol:{span:12}
}
const SendModal = props => {
  const [form] = Form.useForm();
  const { visible,channelOrderNo } = props;
  const [ expressList, setExpressList ] = useState([]);
  useEffect(() => {
    getExpressListApi().then(res => {
      if (res.httpCode == 200) {
        setExpressList(res.result);
      }
    });
  }, []);
  //onOk
  const onOk = async () => {
    const values = await form.validateFields();
    values.channelOrderNo = channelOrderNo;
    props.onOk(values, resetForm);
  };
  const resetForm = () => {
    form.resetFields();
  };
  //
  const onCancel = () => {
    resetForm();
    props.onCancel();
  };
  return (
    <Modal title="" visible={visible} onOk={onOk} onCancel={onCancel}>
      <Form form={form} {...formLayout} >
        <Form.Item label="">
          {channelOrderNo}
        </Form.Item>
        <Form.Item label="" name="logisticsCode">
          <Select placeholder="" allowClear={true}>
            {(expressList&&expressList.length>0)&&
              expressList.map(item => (
                <Select.Option key={item.logisticsid} value={item.logisticscode}>{item.logisticsname}</Select.Option>
              ))}
          </Select>
        </Form.Item>
        <Form.Item
          label=""
          name="trackingNumber"
          rules={[{ required: true, message: "" }]}
        >
          <Input placeholder="" autoComplete='off'/>
        </Form.Item>
      </Form>
    </Modal>
  );
};
export default SendModal;
