class TableItemShowTime extends Component {
	render() {
		return <span>{this.props.showTime && moment(this.props.showTime).format('YYYY-MM-DD H:mm:ss')}</span>;
	}
}

export { DateTime, RangeTime, FilterSearchRangeTime, TableItemShowTime };
