import moment from "moment";
const Columns = [
  { title: "", dataIndex: "attributeName", key: "1" },
  {
    title: "",
    dataIndex: "attributeState",
    key: "2",
    render: (text, record, index) => <span>{text == 0 ? "" : ""}</span>
  },
  {
    title: "",
    dataIndex: "modifyBy",
    key: "3",
    render: (text, record, index) => (
      <div>
        <span>{record.modifyBy}</span>
        <br />
        <span>
          {record.lastUpdateTime &&
            moment(record.lastUpdateTime).format("YYYY-MM-DD HH:mm:ss")}
        </span>
      </div>
    )
  },
  {
    title: "",
    key: "5",
    render: (text, record, index) => (
      <div>
        <a className='theme-color' onClick={record.onOperateClick.bind(this)}></a>
      </div>
    )
  }
];

export default Columns;
