import React, { Component } from "react";
import { Form, Input, Button } from "antd";
import { useEffect, useState } from 'react';
import lodash from 'lodash';

const FormItem = Form.Item;
import "./index.less";

const Discount=({...props})=> {
  let newSource = lodash.cloneDeep(props.promotionRules);
  newSource&&newSource.map((item, index) => item.key = index);
  const handleDelete = index => {
    newSource.splice(index, 1);
    props.upDateList(newSource)
  };
  const add = () => {
    newSource.push({ param: { leastQty: "", giftQty: "" } });
    props.upDateList(newSource)
  };
  const onChange = (e, index, key) => {
    newSource[index]["param"][key] = e.target.value;
    props.upDateList(newSource)
  };
  const validator=(rule, value, index) => {
    if (+value>0) {
        if(+value>99){
          return Promise.reject('99');
        };
        const currentGiftQty = +newSource[index].param.giftQty;//
        if(newSource[index - 1]){
          const prevLeastQty = +newSource[index - 1].param.leastQty;//
          const prevGiftQty = +newSource[index - 1].param.giftQty;//
          if(prevLeastQty){
            if(+value <= prevLeastQty){
              return Promise.reject('');
            };
            if(prevGiftQty&&currentGiftQty){
              const currentDiscount = +value/(+value+currentGiftQty)//
              const prevDiscount = prevLeastQty/(prevLeastQty + prevGiftQty) //
              if(currentDiscount > prevDiscount){
                return Promise.reject('');
              };
            };
          };
        };
        if(newSource[index+1]){
          const nextLeastQty = +newSource[index + 1].param.leastQty;//
          const nextGiftQty = +newSource[index + 1].param.giftQty;//
          if(nextLeastQty){
            if(+value >= nextLeastQty){
              return Promise.reject('');
            };
            if(nextGiftQty&&currentGiftQty){
              const currentDiscount = +value/(+value+currentGiftQty)//
              const nextDiscount = nextLeastQty/(nextLeastQty + nextGiftQty) //
              if(currentDiscount < nextDiscount){
                return Promise.reject('');
              };
            };
          };
        };
      };
      return Promise.resolve();
  }
  const validatorTwo= (rule, value, index) => {
      if (+value>0) {
        if(+value>99){
          return Promise.reject('99');
        };
        const currentLeastQty = +newSource[index].param.leastQty;//
        if(currentLeastQty){
          const currentDiscount = currentLeastQty/(+value+currentLeastQty)//
          if(newSource[index - 1]){
            const prevLeastQty = +newSource[index - 1].param.leastQty;//
            const prevGiftQty = +newSource[index - 1].param.giftQty;//
            if(prevGiftQty && prevLeastQty){
              const prevDiscount = prevLeastQty/(prevLeastQty + prevGiftQty) //
              if(currentDiscount > prevDiscount){
                return Promise.reject('');
              };
            };
          };
          if(newSource[index+1]){
            const nextLeastQty = +newSource[index + 1].param.leastQty;//
            const nextGiftQty = +newSource[index + 1].param.giftQty;//
            if(nextLeastQty && nextGiftQty){
              const nextDiscount = nextLeastQty/(nextLeastQty + nextGiftQty) //
              if(currentDiscount < nextDiscount){
                return Promise.reject('');
              }
            };
          };
        }
      };
      return Promise.resolve();
  }

  return (
    <div className="discountTwo">
      <div className="discount_tips">
        <span></span>
        <br />
        <span>XYX/X+YX/X+Y</span>
      </div>
      {
        newSource.length>0&&newSource.map((item, index) => (
          <div className="step" key={index}>
            <FormItem className="satified_price">
              {index + 1}<span style={{ color: "red" }}>*</span>
               
                <FormItem
                  noStyle
                  name={['fieldValues',index,'param','leastQty']}
                  rules={[
                    { required: true, message: "" },
                    { pattern: /^[1-9]+(\d*)$/,message: "0"},
                    { validator: (rule, value,)=>validator(rule, value, index)}
                  ]}>
                    <Input
                      onChange={(e) => onChange(e, index, "leastQty")}
                      autoComplete="off"
                      style={{ width: "100px" }}/>
                </FormItem>
                  , 
                <FormItem
                  noStyle
                  name={['fieldValues',index,'param','giftQty']}
                  rules={[
                    {required: true, message: "" },
                    {pattern: /^[1-9]+(\d*)$/,message: "0"},
                    {validator: (rule, value,)=>validatorTwo(rule, value, index)}
                  ]}>
                  <Input
                    autoComplete="off"
                    style={{ width: "100px" }}
                    onChange={(e) =>onChange(e, index, "giftQty")}/>
                </FormItem>
                  
                {newSource.length > 1 && (
                  <span className="pointerSty" onClick={() => handleDelete(index)}> </span>
                )}
            </FormItem>
          </div>
        ))
      }
      <div className="discountTwo_add">
        <Button
          disabled={newSource.length == 3}
          type="primary"
          onClick={add}>
          
        </Button>
      </div>
    </div>
  )
}

export default Discount;
