import { Input, Cascader, Form } from "antd";
import React, { useEffect, useState } from "react";
import { getProvinceListApi } from "api/home/CooperateCenter/ShopManage";

const Address = (props) => {
  const [options, setOptions] = useState([]);
  useEffect(() => {
    //
    getProvinceListApi().then((res) => {
      if (res.httpCode == 200) {
        setOptions(res.result);
      }
    });
  }, []);
  //
  const validatorLng = (rule, value) => {
    if (value <= 180 && value >= -180) {
      return Promise.resolve();
    }
    return Promise.reject("");
  };
  //
  const validatorLat = (rule, value) => {
    if (value <= 90 && value >= -90) {
      return Promise.resolve();
    }
    return Promise.reject("");
  };
  return (
    <React.Fragment>
      <Form.Item
        name="areacode"
        label=""
        rules={[{ required: true, message: "" }]}
      >
        <Cascader placeholder="" options={options} />
      </Form.Item>
      <Form.Item
        name="address"
        label=""
        rules={[{ required: true, message: "" }]}
      >
        <Input placeholder="" autoComplete="off" />
      </Form.Item>
      <Form.Item
        name="warp"
        label=""
        rules={[
          { required: true, message: "" },
          { validator: validatorLng },
        ]}
      >
        <Input placeholder="" autoComplete="off" />
      </Form.Item>
      <Form.Item
        name="weft"
        label=""
        rules={[
          { required: true, message: "" },
          { validator: validatorLat },
        ]}
      >
        <Input placeholder="" autoComplete="off" />
      </Form.Item>
      <Form.Item
        name="shAreacode"
        label=""
        rules={[{ required: true, message: "" }]}
      >
        <Cascader placeholder="" options={options} />
      </Form.Item>
      <Form.Item
        name="shAddress"
        label=""
        rules={[{ required: true, message: "" }]}
      >
        <Input placeholder="" />
      </Form.Item>
    </React.Fragment>
  );
};
export default Address;
