import React, { useState, useEffect } from "react";
import {
  Form,
  Input,
  DatePicker,
  Radio,
  Checkbox,
  Select,
  message,
  Button
} from "antd";
import { Qbtn } from "common";
import moment from "moment";
const TextArea = Input.TextArea;
const RadioGroup = Radio.Group;
import {
  savePushApi,
  getInfosApi
} from "api/home/OperateCenter/Coperate/Cpush";
import { GetCategoryApi } from "api/home/BaseGoods";
import linkOption from "./data.js";

const formItemLayout = {
  labelCol: { span: 4 },
  wrapperCol: { span: 20 }
};
const Bpush = props => {
  const [form] = Form.useForm();
  const [pushNow, setPushNow] = useState(1);
  const [alertType, setAlertType] = useState(true);
  const [status, setStatus] = useState(true);
  const [linkInfoType, setLinkInfoType] = useState("");
  const [pushPersonType, setPushPersonType] = useState(0);
  const [categoryLists, setCategoryLists] = useState([]);
  const { id } = props.match.params;
  //
  useEffect(() => {
    getInfoApi();
    getCategoryList();
  }, []);
  /**
   * 
   */
  const getInfoApi = () => {
    const { id } = props.match.params;
    if (id) {
      getInfosApi(id).then(res => {
        if (res.httpCode == 200) {
          const {
            alertType,
            pushTime,
            alertTypeContent,
            pushNow,
            status
          } = res.result;
          let content = {};
          switch (alertType) {
            case 20:
              content.code = alertTypeContent;
              break;
            case 30:
              content.H5Url = alertTypeContent;
              break;
            case 40:
              content.textInfo = alertTypeContent;
              break;
          }
          setPushNow(pushNow);
          setAlertType(alertType);
          setStatus(status);
          form.setFieldsValue({
            ...res.result,
            alertTypeContent: content,
            pushTime: pushNow == 0 ? moment(pushTime) : undefined
          });
        }
      });
    }
  };
  /**
   * 
   */
  const getCategoryList = () => {
    GetCategoryApi({ level: 1, parentId: "" }).then(res => {
      if (res.httpCode == 200) {
        const list = res.result.filter(item => item.status == 1);
        console.log(list);
        setCategoryLists(list);
      }
    });
  };
  /**
   * form
   * @param {*} changedValues
   * @param {*} allValues
   */
  const onValuesChange = (changedValues, allValues) => {
    const { pushNow, alertType, linkInfoType, pushPersonType } = changedValues;
    if (pushNow == 0 || pushNow == 1) {
      if (pushNow == 1) {
        form.resetFields(["pushTime"]);
      }
      setPushNow(pushNow);
    }
    if (alertType) {
      form.resetFields(["alertTypeContent", "linkInfoType", "linkInfo"]);
      setAlertType(alertType);
    }
    if (linkInfoType) {
      setLinkInfoType(linkInfoType);
    }
    if (pushPersonType == 0 || pushPersonType == 1) {
      setPushPersonType(pushPersonType);
    }
  };
  //
  const handleSubmit = async e => {
    const values = await form.validateFields();
    const params = formatValue(values);
    savePushApi(params).then(res => {
      if (res.httpCode == 200) {
        goBack();
      }
    });
  };
  //
  const formatValue = values => {
    const {
      pushTime,
      alertTypeContent,
      ..._values
    } = values;
    if (_values.pushPersonType == 1) {
      const pushPer = _values.pushPerson.replace(/\s+/g, "").split("\n");
      if (pushPer.length > 10000) {
        return message.error("10000", 0.8);
      }
      pushPer.map(item => {
        if (item.length > 11) {
          return message.error("", 0.8);
        }
      });
    }else{
      _values.pushPerson = 0;
    }
    for (const key in alertTypeContent) {
      if (alertTypeContent[key]) {
        _values.alertTypeContent = alertTypeContent[key];
      }
    }
    if (id) {
      _values.status = status;
      _values.bsPushId = id;
    } else {
      _values.status = 10;
    }
    if (pushTime) {
      _values.pushTime = moment(values.pushTime).format("YYYY-MM-DD HH:mm:ss");
    }
    _values.pushType = 20;
    return _values;
  };
  /**
   * 
   */
  const goBack = () => {
    props.history.push("/account/c_push");
  };

  const radioStyle = {
    display: "block",
    height: "30px",
    lineHeight: "30px",
    marginBottom: "20px"
  };
  return (
    <div className="oms-common-addEdit-pages">
      <Form
        onValuesChange={onValuesChange}
        form={form}
        {...formItemLayout}
        className="common-addEdit-form"
      >
        <Form.Item
          label=""
          name="title"
          rules={[{ required: true, message: "" }]}
        >
          <Input
            placeholder="10"
            maxLength="10"
            autoComplete="off"
          />
        </Form.Item>
        <Form.Item label="">
          <Form.Item
            name="pushNow"
            noStyle
            rules={[{ required: true, message: "" }]}
          >
            <RadioGroup>
              <Radio style={radioStyle} value={1}>
                
              </Radio>
              <Radio style={radioStyle} value={0}>
                
              </Radio>
            </RadioGroup>
          </Form.Item>
          <Form.Item
            noStyle
            name="pushTime"
            rules={[
              {
                required: pushNow == 0,
                message: ""
              },
              {
                validator: (rule, value) => {
                  if (pushNow == 0) {
                    const time = new Date(value).getTime();
                    const currentTime = new Date().getTime();
                    if (time >= currentTime) {
                      return Promise.resolve();
                    }
                    return Promise.reject("");
                  }
                  return Promise.resolve();
                }
              }
            ]}
          >
            <DatePicker
              disabled={pushNow !== 0}
              showTime
              format="YYYY-MM-DD HH:mm:ss"
            />
          </Form.Item>
        </Form.Item>
        <Form.Item
          label=""
          name="msgContent"
          rules={[{ required: true, message: "" }]}
        >
          <TextArea
            className="ant-textarea"
            placeholder="30"
            maxLength="30"
            rows={6}
          />
        </Form.Item>
        <Form.Item label="">
          <Form.Item
            noStyle
            name="alertType"
            rules={[{ required: true, message: "" }]}
          >
            <RadioGroup>
              <Radio style={radioStyle} value={10}>
                
              </Radio>
              <Radio style={radioStyle} value={20}>
                
              </Radio>
              <Radio style={radioStyle} value={30}>
                H5URL
              </Radio>
              <Radio style={radioStyle} value={40}>
                
              </Radio>
            </RadioGroup>
          </Form.Item>
          <div style={{ display: "inline-block", verticalAlign: "top" }}>
            <div style={{ display: "inline-flex" }}>
              <Form.Item
                name="linkInfoType"
                rules={[
                  { required: alertType == 10, message: "" }
                ]}
              >
                <Select
                  placeholder=""
                  disabled={alertType !== 10}
                >
                  {linkOption.map(item => (
                    <Select.Option value={item.key} key={item.key}>
                      {item.value}
                    </Select.Option>
                  ))}
                </Select>
              </Form.Item>
              {alertType == 10 && linkInfoType == 1 && (
                <Form.Item
                  name="linkInfo"
                  rules={[{ required: true, message: "" }]}
                >
                  <Input placeholder="" autoComplete="off" />
                </Form.Item>
              )}
              {alertType == 10 && linkInfoType == 10 && (
                <Form.Item
                  name="linkInfo"
                  rules={[{ required: true, message: "ID" }]}
                >
                  <Input placeholder="ID" autoComplete="off" />
                </Form.Item>
              )}
              {alertType == 10 && linkInfoType == 8 && (
                <Form.Item
                  name="linkInfo"
                  rules={[{ required: true, message: "" }]}
                >
                  <Select placeholder="">
                    {categoryLists.map(el => (
                      <Select.Option key={el.id} value={el.id}>
                        {el.categoryName}
                      </Select.Option>
                    ))}
                  </Select>
                </Form.Item>
              )}
            </div>
            <Form.Item
              name={["alertTypeContent", "code"]}
              rules={[{ required: alertType == 20, message: "" }]}
            >
              <Input disabled={alertType !== 20} autoComplete="off" />
            </Form.Item>
            <Form.Item
              name={["alertTypeContent", "H5Url"]}
              rules={[
                { required: alertType == 30, message: "H5URL" }
              ]}
            >
              <Input disabled={alertType !== 30} autoComplete="off" />
            </Form.Item>
            <Form.Item
              name={["alertTypeContent", "textInfo"]}
              rules={[{ required: alertType == 40, message: "" }]}
            >
              <TextArea
                className="ant-textarea"
                disabled={alertType !== 40}
                placeholder="300"
                maxLength="300"
                rows={6}
              />
            </Form.Item>
          </div>
        </Form.Item>
        <Form.Item label="">
          <Form.Item
            name="pushPersonType"
            noStyle
            rules={[{ required: true, message: "" }]}
          >
            <RadioGroup>
              <Radio style={radioStyle} value={0}>
                
              </Radio>
              <Radio style={radioStyle} value={1}>
                
              </Radio>
            </RadioGroup>
          </Form.Item>
          <Form.Item
            name="pushPerson"
            noStyle
            rules={[
              { required: pushPersonType == 1, message: "" }
            ]}
          >
            <TextArea
              placeholder="10000"
              disabled={pushPersonType !== 1}
              rows={6}
            />
          </Form.Item>
        </Form.Item>
        <div className="handle-operate-save-action">
          <Button onClick={goBack} size='large'></Button>
          <Button type="primary" size='large' onClick={handleSubmit}>
            
          </Button>
        </div>
      </Form>
    </div>
  );
};

export default Bpush;
