import { Modal, Form, Input, Select } from "antd";
import { updateLabelApi } from "api/home/GoodsCenter/BaseConfig/GoodLable";
import React, { useEffect } from "react";

const Option = Select.Option;
const FormItem = Form.Item;

const ExplainModal = props => {
  const [form] = Form.useForm();
  const { tabId, onOk, onCancel, visible } = props;
  useEffect(() => {
    if (tabId) {
      form.setFieldsValue(props);
    }
  });
  const clearForm = () => {
    form.resetFields();
    console.log(form.getFieldsValue());
  };
  const ok = async () => {
    const values = await form.validateFields();
    values.tabName = values.tabName.trim();
    values.tabId = tabId;
    updateLabelApi(values).then(res => {
      if (res.httpCode == 200) {
        onOk(clearForm);
      }
    });
  };
  const cancel = () => {
    onCancel(clearForm);
  };
  return (
    <div>
      <Modal
        title={tabId ? "" : ""}
        visible={visible}
        onOk={ok}
        onCancel={cancel}
        maskClosable={false}
      >
        <Form form={form}>
          <FormItem
            label=""
            name="tabName"
            rules={[{ required: true, message: "15" }]}
            labelCol={{ span: 5 }}
            wrapperCol={{ span: 12 }}
          >
            <Input
              maxLength="4"
              placeholder="4"
              autoComplete="off"
            />
          </FormItem>
          <FormItem
            label=""
            labelCol={{ span: 5 }}
            wrapperCol={{ span: 12 }}
            name="tabStatus"
            rules={[{ required: true, message: "" }]}
          >
            <Select
              allowClear={true}
              placeholder=""
              className="select"
            >
              <Option value={1}></Option>
              <Option value={0}></Option>
            </Select>
          </FormItem>
        </Form>
      </Modal>
    </div>
  );
};
export default ExplainModal;
