import React from 'react'
import {QbaseList, Qbtn, Qpagination, Qtable} from "common/index";
import FilterForm from "./components/FilterForm";
import Columns from "./column";
import {CreateWorkOrder, GetWorkOrder} from "../../../../api/home/CustomerServiceCenter/FromC";
import {Link} from "react-router-dom";
import {ErpExportApi} from "../../../../api/Export";
import {Form, Input, Modal} from "antd";
import {CommonUtils} from "utils/index";

const {TextArea} = Input;
const formRef = React.createRef();

/**
 * 
 */
function showCreateWorkOrder(_this) {
    _this.setState({
        showModal: true
    })
}

/**
 * 
 */
async function createWorkOrderConfirm(_this) {
    _this.showLoading();
    new CreateWorkOrder(CommonUtils.paramsFormValues(formRef)).then(rep => {
        createWorkOrderCancel(_this);
        //
        _this.refreshDataList();
        _this.hideLoading();
    }).catch(er => {
        _this.hideLoading();
    })
}

/**
 * 
 */
function createWorkOrderCancel(_this) {
    _this.setState({
        showModal: false
    })
}

/**
 * 
 *  2020/3/16 11:27
 * LorenWang
 * 
 * 
 * 
 * 
 * 
 */

const WorkOrder = QbaseList((_this) => {
    const {
        dataList, everyPage, currentPage, total
    } = _this.state;
    return (
        <div className="oms-common-index-pages-wrap">
            <FilterForm onSubmit={_this.searchDataList} selectTimeChange={_this.selectTimeChange}/>
            <div className="handle-operate-btn-action">
                <Qbtn size="free" onClick={() => showCreateWorkOrder(_this)}></Qbtn>
            </div>
            <Qtable
                columns={Columns}
                select={true}
                dataSource={dataList}/>
            <Qpagination
                data={{everyPage, currentPage, total}}
                onChange={_this.changePage}/>
            {
                _this.state.showModal && <Modal
                    visible={_this.state.showModal}
                    title=''
                    okText=""
                    onCancel={() => createWorkOrderCancel(_this)}
                    onOk={(e) => createWorkOrderConfirm(_this, e)}
                >
                    <Form ref={formRef}>
                        <Form.Item
                            label=""
                            name="customServiceTheme"
                            labelCol={{span: 5}}
                            wrapperCol={{span: 12}}
                            rules={[{
                                required: true,
                                message: 'Please input your E-mail!',
                            }]}>
                            <Input placeholder=' 15' maxLength='15' autoComplete="off"/>
                        </Form.Item>
                        <Form.Item
                            label=""
                            name="remark"
                            labelCol={{span: 5}}
                            wrapperCol={{span: 12}}
                            rules={[{required: true, message: ''}]}>
                            <TextArea rows={5} placeholder='200' maxLength='200'
                                      autoComplete="off"/>
                        </Form.Item>
                        <Form.Item
                            label="//"
                            name="source"
                            labelCol={{span: 5}}
                            wrapperCol={{span: 12}}>
                            <Input placeholder='64' maxLength='64' autoComplete="off"/>
                        </Form.Item>
                        <Form.Item
                            label=""
                            name="waiterTel"
                            labelCol={{span: 5}}
                            wrapperCol={{span: 12}}>
                            <Input placeholder='32' maxLength='32' autoComplete="off"/>
                        </Form.Item>
                    </Form>
                </Modal>
            }
        </div>
    )
}, GetWorkOrder, false);
export default WorkOrder;
