import React, { useState, useEffect } from "react";
import { Form, Input, Button, Modal } from "antd";
import UploadList from "common/QupLoadImgLimt";
import { saveApi, getInfoApi } from "api/home/GoodsCenter/Cgoods/ActExchangeGoods";
import "./index.less";
const formItemLayout = {
  labelCol: { span: 4 },
  wrapperCol: { span: 20 }
};
const AddExchangeGoods = props => {
  const [form] = Form.useForm();
  const [fileList, setFileList] = useState([]);
  const { id } = props.match.params;
  
  //
  useEffect(() => {
    const { id } = props.match.params;
    if (id) {
      getInfoApi(id).then(res => {
        if (res.httpCode == 200) {
          const { picUrl, ...infos } = res.result;
          const fileList = [
            {
              uid: "-1",
              name: "image.png",
              status: "done",
              url: sessionStorage.getItem("oms_fileDomain") + picUrl,
              img:picUrl
            }
          ];
          setFileList(fileList);
          infos.picUrl = fileList;
          form.setFieldsValue(infos);
        }
      });
    }
  }, []);
  const goBack=()=>{
    props.history.push('/account/event_exchange_products')
  }
  const handleSubmit=async()=>{
    const values = await form.validateFields();
    values.pdSpuActiveId = id;
    values.picUrl = values.picUrl[0].response? values.picUrl[0].response.result:values.picUrl[0].img;
    saveApi(values).then(res=>{
      if(res.httpCode == 200){
        goBack();
      }
    })
  }
  const upDateList=(fileList)=>{
    setFileList(fileList)
  }
  return (
    <div className="oms-common-addEdit-pages add_theme">
      <Form form={form} {...formItemLayout} className="common-addEdit-form">
        <Form.Item
          label=""
          name="name"
          rules={[{ required: true, message: "" }]}
        >
          <Input
            placeholder=""
            maxLength="30"
            autoComplete="off"
          />
        </Form.Item>
        <Form.Item
          label=""
          rules={[{ required: true, message: "" }]}
        >
         <UploadList upDateList={upDateList} name='picUrl' fileList={fileList} limit={1}/>
        </Form.Item>
        <Form.Item
          label=""
          name="describe"
          rules={[{ required: true, message: "" }]}
        >
          <Input
            placeholder=""
            maxLength="50"
            autoComplete="off"
          />
        </Form.Item>
        <Form.Item
          label=""
          name="price"
          rules={[
            { required: true, message: "" },
            { pattern: /^\d+(\.\d+)?$/, message: "" }
          ]}
        >
          <Input placeholder="" autoComplete="off" />
        </Form.Item>
        <Form.Item
          label=""
          name="valueQty"
          rules={[
            { required: true, message: "" },
            { pattern: /^[0-9]*$/, message: "" }
          ]}
        >
          <Input placeholder="" autoComplete="off" />
        </Form.Item>
        <Form.Item
          label=""
          name="convertibleQty"
          rules={[
            { required: true, message: "" },
            { pattern: /^[0-9]*$/, message: ""}
          ]}
        >
          <Input placeholder='' autoComplete="off" disabled={Boolean(props.data)}/>
        </Form.Item>
        <div className="handle-operate-save-action">
          <Button onClick={goBack} size="large">
            
          </Button>
          <Button type="primary" size="large" onClick={handleSubmit}>
            
          </Button>
        </div>
      </Form>
    </div>
  );
};

export default AddExchangeGoods;
