import { Input, Form, Select, Button, DatePicker } from "antd";
import { Link } from 'react-router-dom';

const Columns = [
  {
    title: "",
    dataIndex: "couponCode"
  },
  {
    title: "",
    dataIndex: "couponName",
    render:(text,record,index)=> {
      return <Link
              to={`/account/coupon/info/${record.couponId}`}>
              {record.couponName}
            </Link>
    }
  },
  {
    title: "",
    dataIndex: "couponUseSceneStr"
  },
  {
    title: "",
    dataIndex: "couponMoney"
  },
  {
    title: "",
    dataIndex: "couponFullAmount"
  },
  {
    title: "",
    dataIndex: "couponCount",
    render: (text, record) => <span> {text === -1 ? "" : text}</span>
  },
  {
    title: "",
    dataIndex: "couponGiveCount",
    render: (text, record) => {
      return <Link to={`/account/couponRecord/${record.couponCode}`}>
              {text}
            </Link>
    }
  },
  {
    title: "",
    dataIndex: "couponUsedQty"
  },
  {
    title: "",
    dataIndex: "statusStr"
  },
  {
    title: "",
    dataIndex: "isExpire",
    render:(text,record,index)=>(
      <span>{text==1?'':''}</span>
    )
  },
  {
    title: "",
    dataIndex: "creater"
  },
  {
    title: "",
    dataIndex: "createTime"
  },
  {
    title: "",
    dataIndex: "",
    width: "10%",
    render: (text, record, index) => {
      return (
        <div>
          {(record.status == 1 || record.status == 2) &&
            record.couponCount != -1 && (
              <span
                className="pointerSty"
                onClick={()=>record.onOperateClick('supplyAgain')}>
                
              </span>
            )}
          &nbsp;
          {record.status !== 3 && record.fuse && (
            <span
              className="pointerSty"
              onClick={()=>record.onOperateClick('fusing')}>
              
            </span>
          )}
          &nbsp;
          {record.couponUseScene == 2 && record.status !== 3 && (
            <span
              className="pointerSty"
              onClick={()=>record.onOperateClick('logoutTic')}>
              
            </span>
          )}
        </div>
      );
    }
  }
];

const ColumnCb = [
  {
    title: "",
    dataIndex: "budget",
    render: (value, row, index) => {
      const obj = {
        children: (value ? value : "") + "",
        props: {}
      };
      if (index === 0) {
        obj.props.rowSpan = 4;
      }
      if (index === 1) {
        obj.props.rowSpan = 0;
      }
      if (index === 2) {
        obj.props.rowSpan = 0;
      }
      if (index === 3) {
        obj.props.rowSpan = 0;
      }
      return obj;
    }
  },
  {
    title: "",
    dataIndex: "bearerName"
  },
  {
    title: "",
    dataIndex: "proportion",
    render: (text, record, index) => <span>{text}%</span>
  },
  {
    title: "",
    dataIndex: "remark"
  }
];

const ColumnGoods = [
  {
    title: "",
    dataIndex: "pdCode"
  },
  {
    title: "",
    dataIndex: "name"
  },
  {
    title: "",
    dataIndex: "displayName"
  }
];
const ColumnsAdd = (handleBlur)=> {
  return [
    {
      title: "",
      dataIndex: "pdCode",
      render:(text,record,index)=>{
        return <Form.Item
                name={['list',index,'pdCode']}
                rules={[{required: true, message: ''}]}>
                  <Input
                    onBlur={(e)=>handleBlur(e,index)}
                    placeholder=""
                    autoComplete="off"/>
               </Form.Item>
      }
    },
    {
      title: "",
      dataIndex: "name"
    },
    {
      title: "",
      dataIndex: "displayName"
    }
  ];
}

export { Columns, ColumnCb, ColumnGoods, ColumnsAdd };
