import { useEffect } from "react";
import { Modal, Form, Input, Select,Radio } from "antd";
const FormItem = Form.Item;
import { getInfoApi } from "api/home/StockCenter/StoreManage";
const Option = Select.Option;
const TextArea = Input.TextArea;

const AddStoreModal=(props)=> {
  const [form] = Form.useForm()
  const {id,onOk,onCancel,visible} = props;
  useEffect(()=>{
    if(id){
        getInfoApi({id}).then(res=>{
        if(res.httpCode == 200){
          form.setFieldsValue(res.result)
        };
      })
    }
  },[])
  const clearForm = () => {
    form.resetFields();
  };
  const ok = async() => {
    const values = await form.validateFields();
    if(id){
      values.id = id
    };
    onOk(values, clearForm);
  };
  const cancel = () => {
    onCancel(clearForm);
  };
    return (
      <div>
        <Modal
          title={id?'':''}
          visible={visible}
          onOk={ok}
          onCancel={cancel}
        >
          <Form form={form}>
            <FormItem
              label=""
              name="warehouseName"
              rules={[
                { required: true, message: "15" }
              ]}
              labelCol={{ span: 5 }}
              wrapperCol={{ span: 12 }}
            >
              <Input
                maxLength="15"
                placeholder="15"
                autoComplete="off"
              />
            </FormItem>
            <FormItem
              label="C"
              name="displayNameC"
              rules={[
                { required: true, message: "C15" }
              ]}
              labelCol={{ span: 5 }}
              wrapperCol={{ span: 12 }}
            >
              <Input
                maxLength="15"
                placeholder="C15"
                autoComplete="off"
              />
            </FormItem>
            <FormItem
              label="C"
              name="distributionDescribeC"
              rules={[
                { required: true, message: "C20" }
              ]}
              labelCol={{ span: 5 }}
              wrapperCol={{ span: 12 }}
            >
              <Input
                maxLength="15"
                placeholder="C20"
                autoComplete="off"
              />
            </FormItem>
            <FormItem
              label=""
              labelCol={{ span: 5 }}
              wrapperCol={{ span: 12 }}
              name="pushPlatform"
              rules={[{ required: true, message: "" }]}
            >
              <Radio.Group
                allowClear={true}
                placeholder=""
              >
                <Radio value={2}></Radio>
                <Radio value={3}></Radio>
                <Radio value={1}></Radio>
                <Radio value={4}></Radio>
              </Radio.Group>
            </FormItem>
            <FormItem
              label=""
              labelCol={{ span: 5 }}
              wrapperCol={{ span: 12 }}
              name="departureType"
              rules={[{ required: true, message: "" }]}
            >
              <Select
                allowClear={true}
                placeholder=""
              >
                <Option value={1}></Option>
                <Option value={2}></Option>
                <Option value={3}></Option>
              </Select>
            </FormItem>
            <FormItem
              label=""
              labelCol={{ span: 5 }}
              wrapperCol={{ span: 12 }}
              name="warehouseStatus"
              rules={[{ required: true, message: "" }]}
            >
              <Select
                allowClear={true}
                placeholder=""
              >
                <Option value={1}></Option>
                <Option value={2}></Option>
              </Select>
            </FormItem>
          </Form>
        </Modal>
      </div>
    );
}
export default AddStoreModal;
