import React, {useState} from 'react'
import {Modal} from "antd";
import TopTitleDesHeader from "../../components/TopTitleDesHeader";
import {
    QbaseDetail, Qcards,
    Qtable
} from "common/index";
import Columns from "./column";
import {
    GetPurchasingAnalysisData,
    GetPurchasingAnalysisDataList
} from "api/home/DataCenter/BaseData/PurchasingData";
import CommonUtils from "utils/CommonUtils";
import TableDataListUtil from "utils/TableDataListUtil";

/**
 * 
 *  2020/3/20 19:07
 * LorenWang
 * 
 * 
 * 
 * 
 * 
 */
const PurchasingAnalysis = (props) => {
    const [dataInfo, setDataInfo] = useState({
        /**
         * 
         */
        datalist: [],
    });
    /**
     * 
     */
    const [tableList, setTableList] = useState([]);

    /**
     * 
     * @param _this
     */
    const baseDetailComponentCallback = (_this) => {
        _this.showLoading();
        const iRpPurchaseAnalysis = new GetPurchasingAnalysisData().iRpPurchaseAnalysis;
        iRpPurchaseAnalysis.purchaseAmountRate = CommonUtils.dataDifferenceValueComparison(iRpPurchaseAnalysis.purchaseAmount, iRpPurchaseAnalysis.upPurchaseAmount); //
        iRpPurchaseAnalysis.purchaseQtyRate = CommonUtils.dataDifferenceValueComparison(iRpPurchaseAnalysis.purchaseQty, iRpPurchaseAnalysis.upPurchaseQty); //
        iRpPurchaseAnalysis.returnAmountRate = CommonUtils.dataDifferenceValueComparison(iRpPurchaseAnalysis.returnAmount, iRpPurchaseAnalysis.upReturnAmount); //
        iRpPurchaseAnalysis.returnQtyRate = CommonUtils.dataDifferenceValueComparison(iRpPurchaseAnalysis.returnQty, iRpPurchaseAnalysis.upReturnQty); //
        const datalist = [{
            title: '',
            value: iRpPurchaseAnalysis.purchaseAmount,
            rate: Math.abs(iRpPurchaseAnalysis.purchaseAmountRate),
            text: '',
            type: (iRpPurchaseAnalysis.purchaseAmountRate < 0) ? '0' : '1'
        }, {
            title: '',
            value: iRpPurchaseAnalysis.purchaseQty,
            rate: Math.abs(iRpPurchaseAnalysis.purchaseQtyRate),
            text: '',
            type: (iRpPurchaseAnalysis.qbyQtyRate < 0) ? '0' : '1'
        }, {
            title: '',
            value: iRpPurchaseAnalysis.returnAmount,
            rate: Math.abs(iRpPurchaseAnalysis.returnAmountRate),
            text: '',
            type: (iRpPurchaseAnalysis.returnAmountRate < 0) ? '0' : '1'
        }, {
            title: '',
            value: iRpPurchaseAnalysis.returnQty,
            rate: Math.abs(iRpPurchaseAnalysis.returnQtyRate),
            text: '',
            type: (iRpPurchaseAnalysis.returnQtyRate < 0) ? '0' : '1'
        }];
        setDataInfo({
            datalist:datalist
        });

        let tableList = new GetPurchasingAnalysisDataList().analysis;
        setTableList(TableDataListUtil.addKeyAndResultList(tableList));
        _this.hideLoading()
    };

    /**
     * 
     */
    const desInfo = () => {
        Modal.info({
            title: '',
            content: (
                <div className='lists'>
                    <p></p>
                    <p></p>
                    <p></p>
                    <p></p>
                    <p>QQ</p>
                    <p>/1030%</p>
                    <p> =-/</p>
                </div>
            ),
            onOk() {
            },
        });
    };

    return <QbaseDetail childComponent={<div>
        <TopTitleDesHeader isShowUpdateTime={false} desInfoClick={desInfo}/>
        <Qcards data={dataInfo.datalist}/>
        <div></div>
        <div className="oms-common-index-pages-wrap">
            <Qtable
                columns={Columns}
                select={true}
                dataSource={tableList}/>
        </div>
    </div>} baseDetailComponentCallback={baseDetailComponentCallback}/>
};
export default PurchasingAnalysis;
