import { Input, Row, Col, Select, Form } from "antd";
import { BaseFilter, Qbtn } from "common";
const FormItem = Form.Item;
const { Option } = Select;

class Search extends BaseFilter {
  formRef = React.createRef();
  constructor(props) {
    super(props);
    this.state = {};
  }
  handleSubmit = async () => {
    const values = await this.formRef.current.validateFields();
    this.props.onSubmit(values);
  };
  //
  Category1 = () => {
    const { level } = this.props;
    return (
      <React.Fragment>
        <Col {...this.colspans}>
          <FormItem name="categoryName" label="">
            <Input placeholder="" autoComplete="off" />
          </FormItem>
        </Col>
        <Col {...this.colspans}>
          <FormItem label="" name="status">
            <Select
              allowClear={true}
              placeholder=""
              className="select"
            >
              <Option value={0}></Option>
              <Option value={1}></Option>
            </Select>
          </FormItem>
        </Col>
      </React.Fragment>
    );
  };
  //
  Category2 = () => {
    const { level } = this.props;
    return (
      <React.Fragment>
        <Col {...this.colspans}>
          <FormItem label="" name="categoryName">
            <Input placeholder="" autoComplete="off" />
          </FormItem>
        </Col>
        <Col {...this.colspans}>
          <FormItem label="" name="categoryName2">
            <Input placeholder="" autoComplete="off" />
          </FormItem>
        </Col>
        <Col {...this.colspans}>
          <FormItem label="" name="status">
            <Select
              allowClear={true}
              placeholder=""
              className="select"
            >
              <Option value={0}></Option>
              <Option value={1}></Option>
            </Select>
          </FormItem>
        </Col>
      </React.Fragment>
    );
  };
  //
  Category3 = () => {
    const { level } = this.props;
    return (
      <React.Fragment>
        <Col {...this.colspans}>
          <FormItem name="categoryName" label="">
            <Input placeholder="" autoComplete="off" />
          </FormItem>
        </Col>
        <Col {...this.colspans}>
          <FormItem name="categoryName2" label="">
            <Input placeholder="" autoComplete="off" />
          </FormItem>
        </Col>
        <Col {...this.colspans}>
          <FormItem name="categoryName3" label="">
            <Input placeholder="" autoComplete="off" />
          </FormItem>
        </Col>
        <Col {...this.colspans}>
          <FormItem name="status" label="">
            <Select
              allowClear={true}
              placeholder=""
              className="select"
            >
              <Option value={0}></Option>
              <Option value={1}></Option>
            </Select>
          </FormItem>
        </Col>
      </React.Fragment>
    );
  };
  //
  Category4 = () => {
    const { level } = this.props;
    return (
      <React.Fragment>
        <Col {...this.colspans}>
          <FormItem name="categoryName" label="">
            <Input placeholder="" autoComplete="off" />
          </FormItem>
        </Col>
        <Col {...this.colspans}>
          <FormItem name="categoryName2" label="">
            <Input placeholder="" autoComplete="off" />
          </FormItem>
        </Col>
        <Col {...this.colspans}>
          <FormItem name="categoryName3" label="">
            <Input placeholder="" autoComplete="off" />
          </FormItem>
        </Col>
        <Col {...this.colspans}>
          <FormItem name="categoryName4" label="">
            <Input placeholder="" autoComplete="off" />
          </FormItem>
        </Col>
        <Col {...this.colspans}>
          <FormItem name="status" label="">
            <Select
              allowClear={true}
              placeholder=""
              className="select"
            >
              <Option value={0}></Option>
              <Option value={1}></Option>
            </Select>
          </FormItem>
        </Col>
      </React.Fragment>
    );
  };
  render() {
    const { level } = this.props;
    return (
      <div className="qtoolOms-condition">
        <Form {...this.formItemLayout} ref={this.formRef} className="serach-common-form">
          <Row gutter={24}>
            {level == 1 && this.Category1()}
            {level == 2 && this.Category2()}
            {level == 3 && this.Category3()}
            {level == 4 && this.Category4()}
          </Row>
        </Form>
        <FormItem className="oms-condition-operate">
          <Qbtn type="primary" onClick={this.handleSubmit.bind(this)}>
            
          </Qbtn>
        </FormItem>
      </div>
    );
  }
}
export default Search;
