
import { Input, Form, DatePicker, Select, Button } from 'antd';
const FormItem = Form.Item;

const ColumnsAdd=(optionSource, onSelect)=>{
  return [
    {
      title: '',
      dataIndex: 'key',
      align:'center',
      width:'4%',
      render:(text,record,index)=> {
        index++;
        return <span>{index}</span>
      }
    }, {
      title: '',
      dataIndex: 'couponId',
      align:'center',
      width:'10%',
      render:(text,record,index)=> {
        return <FormItem name={['couponIds',index,'couponId']} rules={[{ required:true,message:''}]}>
                <Select placeholder="" onSelect={(value,option)=>onSelect(value,index)}>
                  {optionSource && optionSource.map(item => (
                    <Select.Option key={item.couponId} value={item.couponId} >
                      {item.couponName}
                    </Select.Option>
                  ))}
                </Select>
              </FormItem>
      }
    },{
      title: "",
      dataIndex: "couponFullAmount",
      render:(text,record,index)=>{
        return <p>
          {
            text ? <span>{text}</span> : ''
          }
        </p>
      }
    }, {
      title: "",
      dataIndex: "couponMoney",
      render:(text,record,index)=>{
        return <p>
          {
            text ? <span>{text}</span> : ''
          }
        </p>
      }
    },{
      title: "",
      dataIndex: "couponGiveCount"
    }];
}
export default ColumnsAdd;
