import '@ant-design/compatible/assets/index.css';
import {
  Input,Spin,Form,Upload,Select,Table,Card,
  Row,Col,Checkbox,Button,Radio,AutoComplete,
} from 'antd';
import { useState, useEffect } from 'react';
import { Qtable, Qmessage, Qbtn } from 'common';
import { ColumnsInfoGeneral,ColumnsInfoCross } from './columns';
import { GetEditInfoApi } from 'api/home/BaseGoods';
// import './BaseGoodsAdd.less';

let FormItem = Form.Item;
let Option = Select.Option;
const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 4 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
    };
const formItemLayoutBig = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 4 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 20 },
      },
    };

const BaseGoodsAdd =({...props})=> {//productNature12
  let spuCode = props.match.params.id;
  let productNature = props.match.params.type;
  let columnsInfo = productNature==1?ColumnsInfoGeneral:ColumnsInfoCross;
  let [loading,setLoading] =useState(false)
  let [totalData,setTotalData] =useState({})
  let [goodsList,setGoodsList] =useState([])
  //
  const initPage=()=> {
    setLoading(true)
    GetEditInfoApi({spuCode})
    .then((res) => {
      let { list, attrList,...pdSpu} =res.result;
      list&&list.map((el)=>el.key=el.id);
      setTotalData(pdSpu)
      setGoodsList(list);
      setLoading(false)
    })
  }
  //
  const goReturn=()=> {
    props.history.push('/account/items_list')
  }
  useEffect(()=>{
    initPage();
    return () => {
      setTotalData({})
      setGoodsList([])
    };
  },[spuCode])
  console.log(goodsList)
  return (
    <Spin tip="..." spinning={loading}>
      <div className="oms-common-addEdit-pages baseGoods-addEdit-pages">
          <Card title="">
            <Form.Item label="spu">
              {totalData.spuCode}
            </Form.Item>
            <Form.Item label="">
              {totalData.productName}
            </Form.Item>
            <FormItem label=''>
             {totalData.productName}
            </FormItem>
            <Form.Item label="">
              {totalData.brandAddress}
            </Form.Item>
            <Form.Item label=''>
              {totalData.country}
            </Form.Item>
            {
              totalData.productNature==1?
              <div>
                <Form.Item label="">
                  {totalData.productType}
                </Form.Item>
                <Form.Item label="">
                  {totalData.procurementTarget}
                </Form.Item>
              </div>
              :
              <Form.Item label="">
                {totalData.bondedWarehouseId}
              </Form.Item>
            }
            <Form.Item label="">
              {totalData.categoryId}
            </Form.Item>
            <Form.Item label="">
              {totalData.categoryId2}
            </Form.Item>
            <Form.Item label="">
              {totalData.categoryId3}
            </Form.Item>
            <Form.Item label="">
              {totalData.categoryId4}
            </Form.Item>
          </Card>
          {
            productNature==1&&
            <div>
              <Card title="">
                <Form.Item label="">
                  {totalData.profits}
                </Form.Item>
                <Form.Item label="B">
                  {totalData.minBoxSpecification}
                </Form.Item>
                <Form.Item label="">
                  {totalData.sendType}
                </Form.Item>
                {
                  totalData.sendType=='1'&&
                  <Form.Item label="">
                      {totalData.distributionDays}
                  </Form.Item>
                }
              </Card>
              <Card title="">
                <Form.Item label="">
                  {totalData.basicsBoxSpecification}
                </Form.Item>
                <Form.Item label="">
                  {totalData.batchProcessingStatus}
                </Form.Item>
                {
                  totalData.batchProcessingStatus==2&&
                  <div>
                    <Form.Item label="">
                      {totalData.batchProcessingType}
                    </Form.Item>
                    <Form.Item label="">
                      {totalData.lotLimitInDay}
                    </Form.Item>
                  </div>
                }
              </Card>
            </div>
          }
          <Card title="SKU">
            <Qtable
              scroll={{x:'100%'}}
              dataSource={goodsList}
              columns={columnsInfo}/>
          </Card>
          <div className="handle-operate-save-action">
            <Qbtn onClick={goReturn}>  </Qbtn>
          </div>
      </div>
    </Spin>
  );
}

export default BaseGoodsAdd;
