import React from "react";
import { Qbtn, BaseFilter } from "common";
import { Form, Row, Col, Input, Select, DatePicker } from "antd";
import { searchStoreApi } from "api/home/OrderCenter/PurchaseOrder/PurchaseIn";
const Option = Select.Option;
const RangePicker = DatePicker.RangePicker;
const formItemLayout = {
  labelCol: {
    span: 10,
  },
  wrapperCol: {
    span: 12,
  },
};

class NormalForm extends BaseFilter {
  constructor(props) {
    super(props);
    this.state = {
      storeList: [],
    };
    this.colspans = {
      xs: 24,
      md: 12,
      xl: 8,
      xxl: 6,
    };
  }
  formRef = React.createRef();
  componentDidMount = () => {
    searchStoreApi().then((res) => {
      if (res.httpCode == 200) {
        this.setState({
          storeList: res.result,
        });
      }
    });
  };

  //
  render() {
    const { storeList } = this.state;
    return (
      <div className="qtoolOms-condition">
        <Form ref={this.formRef} className="serach-common-form">
          <Row>
            <Col {...this.colspans}>
              <Form.Item
                name="orderDetailNo"
                label=""
                {...formItemLayout}
              >
                <Input placeholder="" autoComplete="off" />
              </Form.Item>
            </Col>
            <Col {...this.colspans}>
              <Form.Item
                name="channelOrderNo"
                label=""
                {...formItemLayout}
              >
                <Input placeholder="" autoComplete="off" />
              </Form.Item>
            </Col>
            <Col {...this.colspans}>
              <Form.Item
                name="outerProductCode"
                label=""
                {...formItemLayout}
              >
                <Input placeholder="" autoComplete="off" />
              </Form.Item>
            </Col>
            <Col {...this.colspans}>
              <Form.Item name="skuCode" label="" {...formItemLayout}>
                <Input placeholder="" autoComplete="off" />
              </Form.Item>
            </Col>
            <Col {...this.colspans}>
              <Form.Item
                name="productName"
                label=""
                {...formItemLayout}
              >
                <Input placeholder="" autoComplete="off" />
              </Form.Item>
            </Col>
            <Col {...this.colspans}>
              <Form.Item name="consignee" label="" {...formItemLayout}>
                <Input placeholder="" autoComplete="off" />
              </Form.Item>
            </Col>
            <Col {...this.colspans}>
              <Form.Item name="phone" label="" {...formItemLayout}>
                <Input placeholder="" autoComplete="off" />
              </Form.Item>
            </Col>
            <Col {...this.colspans}>
              <Form.Item name="idNumber" label="" {...formItemLayout}>
                <Input placeholder="" autoComplete="off" />
              </Form.Item>
            </Col>
            <Col {...this.colspans}>
              <Form.Item
                name="orderStatus"
                label=""
                {...formItemLayout}
              >
                <Select allowClear={true} placeholder="">
                  <Option value={1}></Option>
                  <Option value={2}></Option>
                  <Option value={3}></Option>
                </Select>
              </Form.Item>
            </Col>
            <Col {...this.colspans}>
              <Form.Item
                label=""
                name="warehouseCode"
                {...formItemLayout}
              >
                <Select placeholder="">
                  {storeList.length > 0 &&
                    storeList.map((item) => (
                      <Select.Option value={item.warehouseCode} key={item.id}>
                        {item.warehouseName}
                      </Select.Option>
                    ))}
                </Select>
              </Form.Item>
            </Col>
            <Col {...this.colspans}>
              <Form.Item label="" name="time" {...formItemLayout}>
                <RangePicker showTime format="YYYY-MM-DD HH:mm:ss" />
              </Form.Item>
            </Col>
            <Col span={24}>
              <Form.Item className="oms-condition-operate">
                <Qbtn type="primary" onClick={this.handleSubmit.bind(this)}>
                  
                </Qbtn>
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </div>
    );
  }
}

export default NormalForm;
