import React, {useEffect, useState} from 'react'
import {
    GetPurchaseInOrderDetailApi,
    GetPurchaseInOrderOptionsLogsApi
} from "../../../../api/home/OrderCenter/PurchaseOrder/PurchaseIn";
import {Card, Col, Form, Row} from "antd";
import {Qtable, QdetailBaseInfo, QbaseInfo} from "common/index";
import GoodsColumns from "./column/Goods";
import OrderLogsColumns from "./column/OrderLogs";
import {
    AUDIT_STATUS_PASS,
    AUDIT_STATUS_WAIT,
    DOCUMENT_TYPE_GOODS_OUT_OF_STOCK_PRESSURE,
    DOCUMENT_TYPE_NEW,
    DOCUMENT_TYPE_NORMAL,
    DOCUMENT_TYPE_SPECIMEN,
    LOGISTICS_EXPENSE_MODE_RECIPIENT_PAY,
    LOGISTICS_EXPENSE_MODE_ZERO
} from "./config";
import moment from "moment";
import TableDataListUtil from "utils/TableDataListUtil";

/**
 * 
 *  2020/3/7 13:53
 * LorenWang
 * 
 * 
 * 
 * 
 * 
 */
const PurchaseInDetail = (props) => {
    const [orderLogs, setOrderLogs] = useState([]);
    const [goodsList, setGoodsList] = useState([]);
    const [dataInfo, setDataInfo] = useState({});
    /**
     * 
     */
    useEffect(() => {
        const {id} = props.match.params;
        showLoading();
        //
        new GetPurchaseInOrderDetailApi({stockingCode: id}).then(res => {
            hideLoading();
            //
            setDataInfo(res.result);
            //
            setGoodsList(TableDataListUtil.addKeyAndResultList(res.result.data));
        });
        //
        new GetPurchaseInOrderOptionsLogsApi(id).then(res => {
            hideLoading();
            setOrderLogs(TableDataListUtil.addKeyAndResultList(res.result));
        });
    }, []);
    /**
     * 
     */
    const showLoading = () => {

    };
    /**
     * 
     */
    const hideLoading = () => {

    };
    return (
        <div className="oms-common-addEdit-pages bgood_add">
            <Card title="">
                <QbaseInfo dataInfo={
                    [{key: "", value: dataInfo.stockingCode},
                        {key: "", value: dataInfo.procurementTarget},
                        {key: "", value: dataInfo.suppliersName},
                        {
                            key: "",
                            value: dataInfo.documentType === DOCUMENT_TYPE_NEW ? "" :
                                (dataInfo.documentType === DOCUMENT_TYPE_NORMAL ? "" :
                                    (dataInfo.documentType === DOCUMENT_TYPE_GOODS_OUT_OF_STOCK_PRESSURE ? "" :
                                        (dataInfo.documentType === DOCUMENT_TYPE_SPECIMEN ? "" : "")))
                        },
                        {key: "", value: dataInfo.warehouseName},
                        {key: "", value: dataInfo.statusStr},
                        {key: "", value: dataInfo.stepStr},
                        {key: "", value: dataInfo.itemCount},
                        {
                            key: "",
                            value: moment(dataInfo.predictCtime).format("YYYY-MM-DD")
                        },
                        {
                            key: "",
                            value: dataInfo.logisticsType === LOGISTICS_EXPENSE_MODE_ZERO ? "" :
                                (dataInfo.logisticsType === LOGISTICS_EXPENSE_MODE_RECIPIENT_PAY ? "" + dataInfo.postage
                                    + "" : "")
                        },
                        {key: "", value: dataInfo.user},
                        {
                            key: "",
                            value: moment(dataInfo.createTime).format("YYYY-MM-DD HH:mm:ss")
                        },
                        {key: "", value: dataInfo.remarkes},]
                }/>
            </Card>
            <Card title="">
                <Qtable columns={GoodsColumns} dataSource={goodsList}/>
            </Card>
            <Card title="">
                <Qtable columns={OrderLogsColumns} dataSource={orderLogs}/>
            </Card>
        </div>
    )
};
export default PurchaseInDetail
