import React from "react";
import { Form, Modal, Radio, message } from "antd";
import {
  goAuditApi
} from "api/home/GoodsCenter/BaseConfig/GoodsAudit";
const FormItem = Form.Item;
const formItemLayout = {
  labelCol: { span: 6 },
  wrapperCol: { span: 18 }
};

const Audit = (props) => {
  const [form] = Form.useForm();
  const { visible, selectedRowKeys } = props;
  const onOk = async () => {
    const values = await form.validateFields();
    values.examineIds = selectedRowKeys;
    goAuditApi(values).then(res => {
      if (res.httpCode == 200) {
        message.success("");
        props.onOk();
      }
    });
  };
  const onCancel = () => {
    props.onCancel();
  };

  return (
    <Modal
      title=""
      visible={visible}
      onOk={onOk}
      onCancel={onCancel}
      okText=""
    >
      <Form form={form} {...formItemLayout}>
        <FormItem label="" {...formItemLayout}>
          {selectedRowKeys.length}
        </FormItem>
        <FormItem label="" name="status" rules={[{required:true,message:''}]}>
          <Radio.Group>
            <Radio value={2}></Radio>
            <Radio value={3}></Radio>
          </Radio.Group>
        </FormItem>
      </Form>
    </Modal>
  );
};
export default Audit;
