import NP from "number-precision";
import { Sessions } from "utils";

const CommonUtils = {
  clearEmptyObj(obj) {
    let newPdSpu = {};
    for (let key in obj) {
      if (obj[key]) {
        newPdSpu[key] = obj[key];
      }
    }
    return newPdSpu;
  },
  formatToUrlPath(val) {
    if (!val) {
      return val;
    }
    if (val.length == 0) {
      return null;
    }
    if( val instanceof Array == true) {//
      if(val&&val[0].response) {
        let urlPath = val[0].response.result;
        val = urlPath;
      } else {
        val = val[0].path;
      }
    } else {//
      if(val&&val.response) {
        let urlPath = val.response.result;
        val = urlPath;
      } else {
        val = val.path;
      }
    }

    return val;
  },
  formatToFilelist(url,index) {
    let fileDomain = Sessions.get('fileDomain');
    let fileList;
    if (url) {
      fileList = [{
          uid: `-1/${index}`,
          status: "done",
          path: url,
          url: fileDomain + url,
        },
      ];
    } else {
      fileList = [];
    }
    return fileList;
  },
  /**
   *  
   * @param currentValue 
   * @param oldValue 
   * @param isUseMinValue 0num1
   * @returns {number} 
   * [-100~100]
   */
  dataDifferenceValueComparison(currentValue, oldValue) {
    const a = currentValue == null ? 0 : currentValue;
    const b = oldValue == null ? 0 : oldValue;
    let rate = Math.round(NP.divide(NP.minus(a , b), b) * 100);
    return rate;
  },

  /**
   * Form,
   * @param formRef Formref
   */
  async paramsFormValues(formRef) {
    const values = await formRef.current.validateFields();
    console.log(values);
    for (let i in values) {
      // 
      if (typeof values[i] == "string") {
        values[i] = values[i].replace(/^\s+|\s+$/gm, "");
      }
    }
    return values;
  },
};

export default CommonUtils;
