import React from "react";
import {Form, Input, Select, Row, Col} from "antd";
import moment from "moment";
import {BaseFilter, Qbtn} from "common";
import {FilterSearchRangeTime} from "common/QdisabledDateTime";
import {
    WORK_ORDER_HANDLE_TIME_TYPE_FIRST, WORK_ORDER_HANDLE_TIME_TYPE_SECOND,
    WORK_ORDER_HANDLE_TIME_TYPE_THIRD,
    WORK_ORDER_STATUS_END,
    WORK_ORDER_STATUS_IN_HAND,
    WORK_ORDER_STATUS_WAIT
} from "../../config";

const FormItem = Form.Item;
const {Option} = Select;

export default class SearchForm extends BaseFilter {
    formRef = React.createRef();

    render() {
        return (
            <div className="qtoolOms-condition">
                <Form className="serach-common-form" ref={this.formRef}{...this.formItemLayout}>
                    <Row gutter={24}>
                        <Col {...this.colspans}>
                            <FormItem label="" name="customServiceNo" {...this.formItemLayout}>
                                <Input placeholder="" autoComplete="off"/>
                            </FormItem>
                        </Col>
                        <Col {...this.colspans}>
                            <FormItem name="customServiceTheme"
                                      label="" {...this.formItemLayout}>
                                <Input placeholder="" autoComplete="off"/>
                            </FormItem>
                        </Col>
                        <Col {...this.colspans}>
                            <FormItem name="waiter" label="" {...this.formItemLayout}>
                                <Input placeholder="" autoComplete="off"/>
                            </FormItem>
                        </Col>
                        <Col {...this.colspans}>
                            <FormItem label="" name="status"   {...this.formItemLayout}>
                                <Select placeholder="" allowClear={true}>
                                    <Option value={WORK_ORDER_STATUS_WAIT}></Option>
                                    <Option value={WORK_ORDER_STATUS_IN_HAND}></Option>
                                    <Option value={WORK_ORDER_STATUS_END}></Option>
                                </Select>
                            </FormItem>
                        </Col>
                        <Col {...this.colspans}>
                            <FormItem label="" name="handleTimeType"   {...this.formItemLayout}>
                                <Select placeholder="" allowClear={true}>
                                    <Option
                                        value={WORK_ORDER_HANDLE_TIME_TYPE_FIRST}>0-5h</Option>
                                    <Option
                                        value={WORK_ORDER_HANDLE_TIME_TYPE_SECOND}>5-24h</Option>
                                    <Option
                                        value={WORK_ORDER_HANDLE_TIME_TYPE_THIRD}>24h</Option>
                                </Select>
                            </FormItem>
                        </Col>
                        <Col {...this.colspans}>
                            <FilterSearchRangeTime
                                selectTimeChange={this.props.selectTimeChange}
                                defaultValue={[moment(this.searchCriteriaDefaultStartTime), moment(this.searchCriteriaDefaultEndTime)]}
                                startTimeName="createTimeST" endTimeName="createTimeET"
                                label=""
                                itemLayout={this.formItemLayout}/>
                        </Col>
                    </Row>
                </Form>
                <Col offset={21}>
                    <FormItem className="oms-condition-operate">
                        <Qbtn type="primary" onClick={this.handleSubmit}>
                            
                        </Qbtn>
                    </FormItem>
                </Col>
            </div>
        );
    }
}
