import React, { useState, useEffect } from "react";
import { Row, Col, Collapse } from "antd";
import DetailSecond from "./components/DetailSecond";
import { getInfosApi } from "api/home/ChannelManage/Statistics";
const { Panel } = Collapse;

const OffLineStatisticInfo = props => {
  //
  const getParams =(props)=>{
    const temp = props.location.search.slice(1).split("&");
    let obj = {}
    temp.map(item => {
      const arr = item.split("=");
      obj[arr[0]] = arr[1]
    });
    return obj
  }
  const { id,source } = getParams(props);
  const [info, setInfos] = useState({});
  useEffect(() => {
    getInfo();
  },[]);
  //
  const getInfo = () => {
    getInfosApi(id).then(res => {
      if (res.httpCode == 200) {
        setInfos(res.result);
      }
    });
  };
  return (
    <div>
      <Collapse  defaultActiveKey={[1,2]}>
        <Panel key={1} header="">
          <div>
            <Row>
              <Col span={6}>ID{info.channelPopularizeCoding}</Col>
              <Col span={6}>{info.name}</Col>
              <Col span={6}>
                {source == 2 ? "" : ""}
              </Col>
              {source == 2 ? (
                <Col span={6}>{info.principal}</Col>
              ) : (
                <Col span={6}>{info.provinceName}</Col>
              )}
              <Col span={6}>{info.levelTwoQty}</Col>
              {source == 2 && <Col span={6}>{info.remark}</Col>}
            </Row>
          </div>
        </Panel>
        <Panel key={2}  header="">
          <DetailSecond source={source} id={id}/>
        </Panel>
      </Collapse>
    </div>
  );
};

export default OffLineStatisticInfo;
