import React from "react";
import {Form, Input, Select, Row, Col} from "antd";
import {BaseFilter, Qbtn} from "common";
import moment from "moment";
import {
    SALE_IN_OUT_COST_TYPE_SALES_OF_THE_REFUND,
    SALE_IN_OUT_COST_TYPE_SALES_RECEIPTS,
    SALE_IN_OUT_DELIVERY_TYPE_EXPRESS_MAIL,
    SALE_IN_OUT_DELIVERY_TYPE_INTRA_CITY_SERVICE,
    SALE_IN_OUT_DELIVERY_TYPE_STORES_TO_THE,
    SALE_IN_OUT_ORDER_STATE_CANCELED,
    SALE_IN_OUT_ORDER_STATE_OFF_THE_STOCKS, SALE_IN_OUT_ORDER_STATE_REFUNDED,
} from "../../config";
import {FilterSearchRangeTime} from "common/QdisabledDateTime";

const FormItem = Form.Item;
const {Option} = Select;

export default class SearchForm extends BaseFilter {
    formRef = React.createRef();

    render() {
        return (
            <div className="qtoolOms-condition">
                <Form className="serach-common-form" ref={this.formRef}{...this.formItemLayout}>
                    <Row gutter={24}>
                        <Col {...this.colspans}>
                            <FormItem label="" name="spShopName" {...this.formItemLayout}>
                                <Input placeholder="" autoComplete="off"/>
                            </FormItem>
                        </Col>
                        <Col {...this.colspans}>
                            <FormItem name="deliveryType" label="" {...this.formItemLayout}>
                                <Select placeholder="" allowClear={true}>
                                    <Option
                                        value={SALE_IN_OUT_DELIVERY_TYPE_STORES_TO_THE}></Option>
                                    <Option
                                        value={SALE_IN_OUT_DELIVERY_TYPE_INTRA_CITY_SERVICE}></Option>
                                    <Option
                                        value={SALE_IN_OUT_DELIVERY_TYPE_EXPRESS_MAIL}></Option>
                                </Select>
                            </FormItem>
                        </Col>
                        <Col {...this.colspans}>
                            <FormItem label="" name="incomeStatus"   {...this.formItemLayout}>
                                <Select placeholder="" allowClear={true}>
                                    <Option
                                        value={SALE_IN_OUT_ORDER_STATE_OFF_THE_STOCKS}></Option>
                                    <Option value={SALE_IN_OUT_ORDER_STATE_CANCELED}></Option>
                                    <Option value={SALE_IN_OUT_ORDER_STATE_REFUNDED}></Option>
                                </Select>
                            </FormItem>
                        </Col>
                        <Col {...this.colspans}>
                            <FormItem label="" name="costType"   {...this.formItemLayout}>
                                <Select placeholder="" allowClear={true}>
                                    <Option
                                        value={SALE_IN_OUT_COST_TYPE_SALES_RECEIPTS}></Option>
                                    <Option
                                        value={SALE_IN_OUT_COST_TYPE_SALES_OF_THE_REFUND}></Option>
                                </Select>
                            </FormItem>
                        </Col>
                        <Col {...this.colspans}>
                            <FilterSearchRangeTime
                                selectTimeChange={this.props.selectTimeChange}
                                defaultValue={[moment(this.searchCriteriaDefaultStartTime), moment(this.searchCriteriaDefaultEndTime)]}
                                startTimeName="dateTimeST" endTimeName="dateTimeET" label=""
                                itemLayout={this.formItemLayout}/>
                        </Col>
                    </Row>
                </Form>
                <Col offset={21}>
                    <FormItem className="oms-condition-operate">
                        <Qbtn type="primary" onClick={this.handleSubmit}>
                            
                        </Qbtn>
                    </FormItem>
                </Col>
            </div>
        );
    }
}
