import { Row, Col, Table } from 'antd';
import './index.less';

const columns = [
  {
    title: '',
    dataIndex: 'pdCode',
  },
  {
    title: '',
    dataIndex: 'pdName',
  },
  {
    title: 'B',
    dataIndex: 'sellPrice',
  },
  {
    title: '',
    dataIndex: 'maxQty',
  },
  {
    title: 'B',
    dataIndex: 'toBQty',
  },
  {
    title: 'C',
    dataIndex: 'toCQty',
  },
];
function YHModTwo({...props}){
  const { list, promotionType } =props;
  return <div>
    {
      list.length>0&&list.map((el,index) => (
        <Table
          key={index}
          pagination={false}
          columns={columns}
          dataSource={el.promotionGifts}
          bordered
          rowKey={(record,index)=>index}
          title={() => `${++index}*${
            promotionType==20?el.param.leastAmount:el.param.leastQty}
            ${promotionType==20?"":""}`}/>
      ))
    }
  </div>
}
function YHModOne({...props}){
  const { list, promotionType } =props;
  return <div className="yh-mod-one-wrap">
    {
      list.length>0&&list.map((el,index) => {
        index++;
        if(promotionType==22) {
          return <div className="item-yh" key={index}>{index}*{el.param.leastAmount}{el.param.reduceAmount}</div>
        }else {
          return <div className="item-yh" key={index}>{index}*{el.param.leastQty}{el.param.reduceQty}</div>
        }
      })
    }
  </div>
}
//20. 21. 22 23.
function DetailDiscount({...props}) {
  const { labelCol, wrapperCol, info, rulesList } =props;
  let Mod,TipsMod;
  let promotionType = info.promotionType;
  switch(promotionType) {
    case 22:
    case 23:
      TipsMod =<Row className="item-row">
                  XYY/XY/X
                </Row>
      Mod = YHModOne;
      break;
    case 20:
    case 21:
      TipsMod =<Row className="item-row">
                  <Col span={labelCol}></Col>
                  <Col span={wrapperCol}></Col>
                </Row>
      Mod = YHModTwo;
      break;
  }
  return <div className="detail-mode-wrap">
          {rulesList&&rulesList.length>0&&TipsMod}
          <Mod list={rulesList} promotionType={promotionType}/>
         </div>
}
export default DetailDiscount;
