import { Input, Form, Select, Button, Tooltip } from 'antd';
import { ExclamationCircleOutlined } from '@ant-design/icons';
import moment from 'moment';
import { Sessions } from 'utils'
const FormItem = Form.Item;

const fileDomain = Sessions.get('fileDomain');

let tagsTit =(
    <span>
      
      <img src="" style={{'width':'80px'}}/>
    </span>
);

const sellingPoints =(<Tooltip placement="top" title=''>
                &nbsp;<ExclamationCircleOutlined />
              </Tooltip>);
const pdSpuInv =(<Tooltip placement="top" title='0'>
                B&nbsp;<ExclamationCircleOutlined />
              </Tooltip>);
const outOfStockShopNum =(<Tooltip placement="top" title='B0'>
              &nbsp;<ExclamationCircleOutlined />
              </Tooltip>);
const tags =(<Tooltip placement="top" title={tagsTit}>
              &nbsp;<ExclamationCircleOutlined />
              </Tooltip>);
export function columnsFun(handleBlur){
  return [
    {
      title: '',
      dataIndex: 'key',
      key: 'key',
      align:'center',
      width:'3%',
      render:(text,record,index)=> {
        index++;
        return <span>{index}</span>
      }
    },
    {
      title: 'Spuid',
      dataIndex: 'pdSpuId',
      key: 'pdSpuId',
      width:'8%',
      render:(text,record,index)=> {
        return <FormItem
                name={['fieldsOne',index,'pdSpuId']}
                rules={[
                  { required:true,message:'Spuid' },
                  { pattern:/^\S+$/g,message:'' }
                ]}>
                  <Input onBlur={(e)=>handleBlur(e,record)} maxLength='15' placeholder="Spuid" autoComplete="off"/>
              </FormItem>
      }
    },
    {
     title: '',
     dataIndex: 'picUrl',
     key: 'picUrl',
     align:'center',
     width:'8%',
     render:(text,record,index)=> {

       return <div className="img-wrap">
                {
                  record.pdSpuPic&&
                  <img src={`${fileDomain}/${record.pdSpuPic}`}/>
                }
             </div>
     }
   },
   {
      title: '',
      dataIndex: 'pdSpuName',
      key: 'pdSpuName',
      width:'8%',
    },
    {
      title: '',
      dataIndex: 'pdCategory',
      key: 'pdCategory',
      width:'8%',
    },
    {
      title:sellingPoints,
      dataIndex: 'sellingPoints',
      key: 'sellingPoints',
      align:'center',
      width:'8%',
      render:(text,record,index)=> {
        return <FormItem name={['fieldsOne',index, 'sellingPoints']} rules={[{pattern:/^\S+$/g,message:''}]}>
                <Input maxLength='8' placeholder="8" autoComplete="off"/>
              </FormItem>
      }
    },{
      title:tags,
      dataIndex: 'tags',
      key: 'tags',
      align:'center',
      width:'8%',
      render:(text,record,index)=> {
        return <FormItem name={['fieldsOne',index,'tags']} rules={[{pattern:/^\S+$/g,message:''}]}>
                <Input maxLength='8' placeholder="8" autoComplete="off"/>
              </FormItem>
      }
    },
    {
      title:pdSpuInv,
      dataIndex: 'pdSpuInv',
      key: 'pdSpuInv',
      width:'10%',
    },
    {
      title:outOfStockShopNum,
      dataIndex: 'outOfStockShopNum',
      key: 'outOfStockShopNum',
      width:'8%',
    },
    {
      title: '',
      dataIndex: 'isLine',
      key: 'isLine',
      width:'8%',
      render:(text,record,index) => {
        return  <span>
            {
              !record.isLine?''
            :
              <span>{record.isLine==20?'':''}</span>
            }
            </span>
      }
    },{
      title: '',
      dataIndex: 'isPresellStr',
      key: 'isPresellStr',
      width:'10%',
    },
    {
      title: '',
      dataIndex: 'operation',
      key: 'operation',
      width:'4%',
      render:(text,record,index) => {
        return <span onClick={()=>record.onOperateClick('delete')} className="cr"></span>
      }
    },
  ];
}
