import React, { useState, useEffect } from "react";
import { Form, Input, message, Select, Cascader, Spin } from "antd";
import { ExclamationCircleFilled } from "@ant-design/icons";
import {
  GetPurchaseOutOrderDetailApi,
  addPurchaseOutApi,
  getOrderInfoApi,
  searchStoreApi,
  getProvinceListApi,
} from "api/home/OrderCenter/PurchaseOrder/PurchaseOut";
import { Qbtn } from "common";
import Editable from "./components/Editable";
import NP from "number-precision";
import "./index.less";

const TextArea = Input.TextArea;
const formItemLayout = {
  labelCol: { span: 4 },
  wrapperCol: { span: 20 },
};

const AddPurchaseOut = (props) => {
  const [form] = Form.useForm();
  const [storeList, setStoreList] = useState([]);
  const [loading, setLoading] = useState(false);
  const [totalPrice, setTotalPrice] = useState(0);
  const [goodList, setGoodList] = useState([]);
  const [options, setOptions] = useState([]);
  const [suppliersName, setSuppliersName] = useState("");
  const { id } = props.match.params;
  useEffect(() => {
    getStoreList();
    initPage();
    getProvinceList();
  }, []);
  /**
   * 
   */
  const initPage = () => {
    if (id) {
      setLoading(false);
      GetPurchaseOutOrderDetailApi(id).then((res) => {
        setLoading(false);
        if (res.httpCode == 200) {
          const { detailList, ...infos } = res.result;
          const {province,area,city} = infos;
          infos.provinces = [province,city,area]
          countPrice(detailList);
          form.setFieldsValue({ goodList: detailList, ...infos });
        }
      });
    }
  };
  const getProvinceList = () => {
    //
    getProvinceListApi().then((res) => {
      if (res.httpCode == 200) {
        setOptions(res.result);
      }
    });
  };

  /**
   * 
   */
  const countPrice = (list) => {
    let totalPrice = 0;
    if (list.length > 0) {
      list.map((item) => {
        item.key = item.id;
        item.total = NP.times(Number(item.amount), Number(item.price)).toFixed(
          2
        );
        totalPrice += Number(item.total);
      });
    }
    setGoodList(list);
    setTotalPrice(totalPrice);
  };
  /**
   * 
   */
  const getStoreList = () => {
    searchStoreApi().then((res) => {
      if (res.httpCode == 200) {
        setStoreList(res.result);
      }
    });
  };
  /**
   * 
   * @param {*} value
   */
  const onSearch = (value) => {
    searchSupplierApi({ sname: value }).then((res) => {
      setSupplierList(res.result);
    });
  };
  /**
   * 
   */
  const handleSubmit = async () => {
    const values = await form.validateFields();
    const { provinces, goodList: data, ..._values } = values;
    debugger
    if (provinces) {
      _values.province = provinces[0];
      _values.city = provinces[1];
      _values.area = provinces[2];
    }
    goodList.map((item, index) => {
      if (props.match.params.id) {
        data[index].id = goodList[index].id;
      }
      data[index].thinkCode = goodList[index].thinkCode;
      data[index].itemCode = goodList[index].itemCode;
    });
    _values.detailList = data;
    _values.stockingReCode = props.match.params.id;
    addPurchaseOutApi(_values).then((res) => {
      if (res.httpCode == 200) {
        goBack();
      }
    });
  };
  //
  const goBack = () => {
    props.history.push("/account/purchaseRefundOrder");
  };
  /**
   *
   */
  const searchInfo = (e) => {
    const { value } = e.target;
    if (value.trim()) {
      getOrderInfoApi({ stockingCode: value.trim() }).then((res) => {
        if (res.httpCode == 200) {
          const { data, suppliersName, ...infos } = res.result;
          data.map((item) => (item.amount = item.returnableNum));
          setSuppliersName(suppliersName);
          countPrice(data);
          form.setFieldsValue({ goodList: data, infos });
        }
      });
    }
  };
  /**
   * goodList
   */
  const changeDataSource = (goodList) => {
    setGoodList([...goodList]);
    let totalPrice = 0;
    goodList.map((item) => {
      totalPrice += Number(item.total);
    });
    setTotalPrice(totalPrice);
  };
  console.log();
  return (
    <Spin spinning={loading}>
      <div className="oms-common-addEdit-pages add_purchase">
        <Form className="common-addEdit-form" form={form} {...formItemLayout}>
          <Form.Item wrapperCol={{span:20,offset:3}}>
            <div className="add_purchaseout_tips">
              <p>
                <ExclamationCircleFilled  style={{ color: "orange" }} />
                1
              </p>
              <p>
                2Crtl +
                FCommand + F
              </p>
            </div>
          </Form.Item>
          <Form.Item className="item_required" label="">
            <Form.Item
              noStyle
              name="stockingCode"
              rules={[{ required: true, message: "" }]}
            >
              <Input
                disabled={id}
                placeholder=""
                onBlur={searchInfo}
                onPressEnter={searchInfo}
                autoComplete="off"
                maxLength="30"
              />
            </Form.Item>
            <span>{suppliersName}</span>
          </Form.Item>
          <Form.Item
            label=""
            name="reRemark"
            rules={[{ required: true, message: " " }]}
          >
            <Input placeholder="" autoComplete="off" />
          </Form.Item>
          <Form.Item label="">
            <Editable
              dataSource={goodList}
              changeDataSource={changeDataSource}
            />
          </Form.Item>
          <Form.Item
            name="warehouseCode"
            label=""
            rules={[{ required: true, message: "" }]}
          >
            <Select placeholder="">
              {storeList.map((item) => (
                <Select.Option value={item.warehouseCode} key={item.id}>
                  {item.warehouseName}
                </Select.Option>
              ))}
            </Select>
          </Form.Item>
          <Form.Item
            label=""
            name="consignee"
            rules={[{ required: true, message: " " }]}
          >
            <Input placeholder="" autoComplete="off" />
          </Form.Item>
          <Form.Item
            label=""
            name="phone"
            rules={[{ required: true, message: "" }]}
          >
            <Input placeholder="" autoComplete="off" />
          </Form.Item>
          <Form.Item label="" className="item_required">
            <Form.Item
              name="provinces"
              noStyle
              label=""
              rules={[{ required: true, message: "" }]}
            >
              <Cascader options={options} placeholder="" />
            </Form.Item>
            <Form.Item
              name="address"
              noStyle
              rules={[{ required: true, message: "" }]}
            >
              <Input
                maxLength="50"
                style={{ width: "300px" }}
                placeholder=""
                autoComplete="off"
              />
            </Form.Item>
          </Form.Item>
          <Form.Item name="remarkes" label="">
            <TextArea rows={7} placeholder="100" />
          </Form.Item>
        </Form>
        <Form.Item wrapperCol={{ offset: 4 }}>
          <div>
            <Qbtn onClick={handleSubmit}></Qbtn>
            <div className="return_price">
              <span>
                
                <span className="return_price_color">{goodList.length}</span>
              </span>
              <span>
                
                <span className="return_price_color">
                  {totalPrice.toFixed(2)}
                </span>
                
              </span>
            </div>
          </div>
        </Form.Item>
      </div>
    </Spin>
  );
};

export default AddPurchaseOut;
