import React, { useEffect, useState } from "react";
import { Card, Form, Radio, Input, Button } from "antd";
import { Qtable } from "common";
import { AbnormalGoodsColumns } from "./columns";
import {
  getInfoApi,
  operateReturnApi
} from "api/home/OrderCenter/Corder/UserReturn/AllReturn";
import moment from "moment";
const TextArea = Input.TextArea;

/**
 *   zhy
 */
const AuditReturnInfo = props => {
  const [form] = Form.useForm();
  const [status, setStatus] = useState("");
  const [infos, setInfos] = useState({});
  const [detailList, setDetailList] = useState("");
  const { id } = props.match.params;
  useEffect(() => {
    getInfoApi({ reOrderNo: id }).then(res => {
      if (res.httpCode == 200) {
        const {detailList,...infos} = result;
        setInfos(infos);
        if(detailList&&detailList.length>0){
          detailList.map((item,index)=>{
            item.key = index
          })
          setDetailList(detailList);
        }
      }
    });
  }, []);
  const onChange = e => {
    setStatus(e.target.value);
  };
  /**
   * 
   */
  const handleSubmit = async () => {
    const values = await form.validateFields();
    values.operation = 1;
    values.reOrderNo=id;
    operateReturnApi(values).then(res => {
      if (res.httpCode == 200) {
        props.history.push("/account/subscriber_refund_orders");
      }
    });
  };
  const formLayout = {
    labelCol:{span:2},
    wrapperCol:{span:8}
  }
  return (
    <div>
      <Card title="" className="base_info">
        <Form.Item label="">{infos.reOrderNo}</Form.Item>
        <Form.Item label="">{infos.channelOrderNo}</Form.Item>
        <Form.Item label="">{infos.deliveryTypeStr}</Form.Item>
        <Form.Item label="">{infos.phone}</Form.Item>
        <Form.Item label="">{infos.subject}</Form.Item>
        <Form.Item label="">{infos.refundTypeStr}</Form.Item>
        <Form.Item label="">{infos.sync}</Form.Item>
        <Form.Item label="">{infos.price}</Form.Item>
        <Form.Item label="">{infos.refundMoney}</Form.Item>
        <Form.Item label="">
          {infos.createTime &&
            moment(infos.createTime).format("YYYY-MM-DD HH:mm:ss")}
        </Form.Item>
      </Card>
      <Card title="">
        <Qtable columns={AbnormalGoodsColumns} dataSource={detailList} />
      </Card>
      <Card title="">
        <Form form={form} {...formLayout}>
          <Form.Item
            name="status"
            label=""
            rules={[{ required: true, message: "" }]}
          >
            <Radio.Group onChange={onChange}>
              <Radio value={20}></Radio>
              <Radio value={99}></Radio>
            </Radio.Group>
          </Form.Item>
          {status == 20 && (
            <React.Fragment>
              <Form.Item
                name="isInvented"
                label=""
                rules={[{ required: true, message: "" }]}
              >
                <Radio.Group>
                  <Radio value={false}></Radio>
                  <Radio value={true}></Radio>
                </Radio.Group>
              </Form.Item>
              <Form.Item
                name="consignee"
                label=""
                rules={[{ required: true, message: "" }]}
              >
                <Input placeholder="" autoComplete="off" />
              </Form.Item>
              <Form.Item
                name="shPhone"
                label=""
                rules={[{ required: true, message: "" }]}
              >
                <Input placeholder="" autoComplete="off" />
              </Form.Item>
              <Form.Item
                name="shAddress"
                label=""
                rules={[{ required: true, message: "" }]}
              >
                <Input placeholder="" autoComplete="off" />
              </Form.Item>
            </React.Fragment>
          )}
          {status == 99 && (
            <Form.Item
              name="refusalReasons"
              label=""
              rules={[{ required: true, message: "" }]}
            >
              <TextArea placeholder="" />
            </Form.Item>
          )}
          <Form.Item>
            <Button type="primary" size="large" onClick={handleSubmit}>
              
            </Button>
          </Form.Item>
        </Form>
      </Card>
    </div>
  );
};
export default AuditReturnInfo;
