import {ImageFlex} from 'common';
import { Row, Col } from 'antd';

function DetailWebShow({...props}) {
  const { labelCol, wrapperCol, info } =props;
  return <div className="detail-mode-wrap">
          <Row className="item-row">
            <Col span={labelCol}></Col>
            <Col span={wrapperCol}><ImageFlex picUrl={info.websiteBanner}/></Col>
          </Row>
          <Row className="item-row">
            <Col span={labelCol}></Col>
            <Col span={wrapperCol}>{info.isBanner?'':''}</Col>
          </Row>
          {
            !!info.isBanner&&
            <div>
              <Row className="item-row">
                <Col span={labelCol}></Col>
                <Col span={wrapperCol}>{info.bannerBeginTime}</Col>
              </Row>
              <Row className="item-row">
                <Col span={labelCol}></Col>
                <Col span={wrapperCol}>{info.bannerTitle}</Col>
              </Row>
              <Row className="item-row">
                <Col span={labelCol}></Col>
                <Col span={wrapperCol}>{info.bannerSubtitle}</Col>
              </Row>
              <Row className="item-row">
                <Col span={labelCol}></Col>
                <Col span={wrapperCol}><ImageFlex picUrl={info.pdDetailBannerPic}/></Col>
              </Row>


            </div>
          }
          <Row className="item-row">
            <Col span={labelCol}>logo</Col>
            <Col span={wrapperCol}>{info.isLogo?'':''}</Col>
          </Row>
          {
            !!info.isLogo&&
            <div>
              <Row className="item-row">
                <Col span={labelCol}>logo</Col>
                <Col span={wrapperCol}>{info.logoBeginTime}</Col>
              </Row>
              <Row className="item-row">
                <Col span={labelCol}>logo</Col>
                <Col span={wrapperCol}><ImageFlex picUrl={info.logoPic}/></Col>
              </Row>
            </div>
          }
          <Row className="item-row">
            <Col span={labelCol}></Col>
            <Col span={wrapperCol}>{info.collation==1?'':'30App+POS'}</Col>
          </Row>
         </div>
}
export default DetailWebShow;
