import React, { useEffect, useState } from "react";
import { Card, Form, Radio, Input, Button } from "antd";
import { Qtable } from "common";
import { AbnormalGoods } from "./columns";
import {
  getInfoApi,
  handelAbnormalApi
} from "api/home/OrderCenter/AbnormalOrder";
const TextArea = Input.TextArea;

const AuditReturnInfo = props => {
  const [form] = Form.useForm()
  const [infos,setInfos] = useState({});
  const [detailList,setDetailList] = useState([]);
  const { id } = props.match.params;
  useEffect(() => {
    getInfoApi({ orderNo: id }).then(res => {
      if (res.httpCode == 200) {
        setInfos(res.result)
        setDetailList(res.result.detailList)
      }
    });
  }, []);
  /**
   * 
   */
  const handleSubmit = async () => {
    const values = await form.validateFields();
    values.operation = 2;
    values.orderNo = id;
    handelAbnormalApi(values).then(res => {
      if (res.httpCode == 200) {
        props.history.push("/account/subscriber_refund_orders");
      }
    });
  };
  console.log(infos)
  return (
    <div>
      <Card title="" className="base_info">
        <Form.Item label="">{infos.id}</Form.Item>
        <Form.Item label="">{infos.channelName}</Form.Item>
        <Form.Item label="">{infos.consignee}</Form.Item>
        <Form.Item label="">{infos.phone}</Form.Item>
        <Form.Item label="">{infos.itemCount}</Form.Item>
        <Form.Item label="">{infos.orderTotal}</Form.Item>
        <Form.Item label="">
          {infos.sortStr}
        </Form.Item>
        <Form.Item label="">{infos.abnormalCause}</Form.Item>
        <Form.Item label="">{infos.consignee}</Form.Item>
        <Form.Item label="">{infos.phone}</Form.Item>
        <Form.Item label="">{infos.address}</Form.Item>
      </Card>
      <Card title="">
        <Qtable columns={AbnormalGoods} dataSource={detailList} />
      </Card>
      <Card title="">
        <Form form={form}>
          <Form.Item name='cancelReasons' label="">
            <TextArea placeholder="" />
          </Form.Item>
          <Form.Item>
            <Button type="primary" size="large" onClick={handleSubmit}>
              
            </Button>
          </Form.Item>
        </Form>
      </Card>
    </div>
  );
};
export default AuditReturnInfo;
