import '@ant-design/compatible/assets/index.css';
import {
  Input,Spin,Form,
  Upload,Select,
  Row,Col,
  Checkbox,Button,
  Radio,AutoComplete,
} from 'antd';
import { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { Qtable, Qbtn, Qmessage } from 'common';
import { GetDetailApi,GetAddApi,GetEditApi} from 'api/home/SupplierManage';
import './SupplierManageAdd.less';

let FormItem = Form.Item;
let Option = Select.Option;
const formItemLayout = {
      labelCol: {
        xs: { span: 4 },
        sm: { span: 4 },
      },
      wrapperCol: {
        xs: { span: 12 },
        sm: { span: 12 },
      },
    };

const SupplierManageAdd=({...props})=> {
  const [form] = Form.useForm();
  const supplierId = props.match.params.id;
  const goReturn=()=> {
    props.history.push('/account/supplierManage')
  }
  const [totalData, setTotal] = useState({cooperationStatus:1});
  const onSubmit = async () => {
    try {
      let  values = await form.validateFields();
      let reqApi=GetAddApi;
      if(supplierId) {
        reqApi = GetEditApi;
        values = {...values,id:supplierId};
      }
      reqApi(values)
      .then((res)=> {
        Qmessage.success('')
        goReturn();
      })
    } catch (errorInfo) {
      console.log('Failed:', errorInfo);
    }
  };
  useEffect(()=>{
    GetDetailApi({supplierId})
    .then((res)=> {
      const { result } =res;
      setTotal(result)
    })
  },[])
  useEffect(()=>{
    form.setFieldsValue(totalData);
  },[totalData])

  return (
    <Spin tip="..." spinning={false}>
      <div className="oms-common-addEdit-pages supplier-Manage-addEdit-pages">
        <Form className="common-addEdit-form" form={form} {...formItemLayout} initialValues={{...totalData}}>
          <div className="part-wrap">
            <p className="title-wrap"><span className="title-name"></span></p>
            <Form.Item
              label=""
              name="name"
              rules={[{ required: true, message: ''}]}>
              <Input placeholder="60" autoComplete="off"/>
            </Form.Item>
            <Form.Item
              label=""
              name="shortName"
              rules={[{ required: true, message: ''}]}>
              <Input placeholder="60" autoComplete="off"/>
            </Form.Item>
            <Form.Item
              label=""
              name="edName">
              <Input placeholder="" autoComplete="off"/>
            </Form.Item>
            <Form.Item
              label=""
              name="tel">
              <Input placeholder="" autoComplete="off"/>
            </Form.Item>
            <Form.Item
              label=""
              name="bank"
              rules={[{ required: true, message: ''}]}>
              <Input placeholder="" autoComplete="off"/>
            </Form.Item>
            <Form.Item
              label=""
              name="bankCard"
              rules={[{ required: true, message: ''}]}>
              <Input placeholder="" autoComplete="off" />
            </Form.Item>
            <Form.Item
              label=""
              name="bankUserName"
              rules={[{ required: true, message: ''}]}>
              <Input placeholder="" autoComplete="off" />
            </Form.Item>
            <Form.Item label="">
              <Form.Item name="accountsType" rules={[{ required: true, message: '' }]}>
                <Radio.Group placeholder="" allowClear={true}>
                  <Radio value={1} key={1}></Radio>
                  <Radio value={2} key={2}></Radio>
                  <Radio value={3} key={3}></Radio>
                </Radio.Group>
              </Form.Item>
              <Form.Item
                noStyle
                shouldUpdate={(prevValues, currentValues) => prevValues.accountsType !== currentValues.accountsType}>
                {({ getFieldValue }) => {
                  return getFieldValue('accountsType') !== 1&&
                  <Form.Item>
                    <Form.Item name="accountsDay" noStyle rules={[{ required: true, message: '' }]}>
                      <Input autoComplete="off" className="short-input"/>
                    </Form.Item>
                    <span></span>
                  </Form.Item>
                }}
              </Form.Item>
            </Form.Item>
            <Form.Item label="" name="cooperationStatus" rules={[{ required: true, message: ''}]}>
              <Select placeholder="" allowClear={true}>
                <Option value={1} key={1}></Option>
                <Option value={2} key={2}></Option>
                <Option value={3} key={3}></Option>
              </Select>
            </Form.Item>
            <Form.Item
              label=""
              name="remark">
              <Input.TextArea placeholder="" rows={4} autoComplete="off"/>
            </Form.Item>
          </div>
          <div className="handle-operate-save-action">
            <Qbtn onClick={goReturn}>
              
            </Qbtn>
            <Qbtn onClick={onSubmit}>
              
            </Qbtn>
          </div>
        </Form>
      </div>
    </Spin>
  );
}

export default SupplierManageAdd;
