import { Form,Input } from 'antd';
import moment from "moment";
import {Link} from "react-router-dom";
import React from "react";

const Columns = [
    {
        title: "",
        dataIndex: "orderNo",
        render: (text, record) => (
            <Link to={`/account/channel_orders/detail/${record.orderNo}`}>{text}</Link>
        )
    },
    {title: "", dataIndex: "shopName"},
    {title: "", dataIndex: "qtySum"},
    {title: "", dataIndex: "amountSum"},
    {title: "", dataIndex: "statusStr"},
    {title: "", dataIndex: "sourceName"},
    {
      title: "",
      render:(text,record,index)=> {
        return<span>
          {record.preSellStatus==1&&""}&nbsp;
          {record.sendType==2&&""}
        </span>
      }
    },
    {title: "", dataIndex: "recName"},
    {title: "", dataIndex: "userName"},
    {
      title: "",
      dataIndex: "createTime",
      render: text => <span>{text && moment(text).format("YYYY-MM-DD HH:mm:ss")}</span>
    },
    {
      title: "",
      dataIndex: "action",
      render:(text,record,index)=> {
        return (
          <span>
            {
              record.status==10&&
              <span className="pointerSty" onClick={() => record.onOperateClick('cancel')}>
                
              </span>
            }
        </span>
        )
      }
    }
];
const ColumnsAdd=(onBlur)=>{
  return [{
            title: "sku",
            dataIndex: "code",
            width:140,
            render:(text,record,index)=> {
              return  <Form.Item
                        name={['list',index,'skuCode']}
                        rules={[{ required: true, message: 'sku'}]}>
                        <Input
                          key={index}
                          onBlur={(e)=>onBlur(e,index,'code')}/>
                      </Form.Item>
            }
          },{
            title: "",
            dataIndex: "qty",
            width:140,
            render:(text,record,index)=> {
              return  <Form.Item
                          name={['list',index,'qty']}
                          rules={[{ required: true, message: ''},
                                { pattern:/^[1-9]+(\d*)$/,message:'' },
                          ]}>
                        <Input
                          onBlur={(e)=>onBlur(e,index,'qty')}
                          key={index}/>
                      </Form.Item>
            }
          },{
            title: "",
            dataIndex: "productName",
            textWrap: 'word-break',
          },{
            title: "",
            dataIndex: "salesAttributeName",
            textWrap: 'word-break',
          },{
            title: "B",
            dataIndex: "businessPrice",
            textWrap: 'word-break',
          },{
            title: "",
            textWrap: 'word-break',
            dataIndex: "amount",
          }]
};
const OrderLogsColumns = [
    {title: "", dataIndex: "operateName"},
    {
      title: "",
      dataIndex: "operateTime",
      render: (text) => (
          <span>{text && moment(text).format("YYYY-MM-DD HH:mm:ss")}</span>
      )
    },
    {title: "", dataIndex: "statusStr"},
    {title: "", dataIndex: "operateUser"},
    {title: "", dataIndex: "remark"},
];
const GoodsColumns = [
    {title: "SKU", dataIndex: "skuCode", key: "1"},
    {title: "", dataIndex: "productName", key: "2"},
    {title: "", dataIndex: "salesAttributeName", key: "3"},
    {title: "", dataIndex: "qty", key: "4"},
    {title: "", dataIndex: "price", key: "5"},
    {title: "", dataIndex: "allAmount", key: "6"},
];
const ShippingInformationColumns = [

    {title: "/", dataIndex: "trackingNumber"},
    {title: "/", dataIndex: "carrier"},
    {title: "", dataIndex: "freightPrice"},
    {title: " ", dataIndex: "status"},
];
const ColumnsReturnAdd=(handleBlur)=> {
  return [{
            title: 'SKU',
            dataIndex: 'skuCode',
          },{
            title: '',
            dataIndex: 'name',
          },{
            title: '',
            dataIndex: 'salesAttributeName',
          },{
            title: '',
            dataIndex: 'qty',
          },{
            title: '',
            dataIndex: 'returnQty',
            width:"150px",
            render: (text, record, index) => {
              const validateQty=(rule,value)=> {
                if(value){
                  if(Number(value) > Number( record.qty )){
                      return Promise.reject('');
                  };
                };
                return Promise.resolve();
              };
              return (
                  <Form.Item
                    name={['list',index,'returnQty']}
                    rules={[{ required: true, message: ''},
                        { pattern:/^[1-9]+(\d*)$/,message:'' },
                        {validator:validateQty}
                    ]}>
                    <Input placeholder="" onBlur={(e)=>handleBlur(e, index)}/>
                  </Form.Item>
              );
            }
          },{
              title: '',
              dataIndex: 'price',
              width:"150px"
          },{
              title: '',
              dataIndex: 'amount',
              width:"150px"
          }];
}
export {
   ColumnsAdd,Columns,
   OrderLogsColumns,GoodsColumns,ShippingInformationColumns,
   ColumnsReturnAdd
}
