const TableDataListUtil = {
    /**
     * key,
     * @param list 
     * @param keyStr keyitemkeyStr
     * @return 
     */
    addKeyAndResultList(list, keyStr = null) {
        //
        const resultList = [];
        //
        try {
            if (list != null) {
                list.forEach((item, index) => {
                    resultList.push({
                        key: keyStr != null && keyStr !== "" ? item[keyStr] : index,
                        ...item
                    })
                });
            }
        } catch (e) {
        }
        return resultList;
    }
};
export default TableDataListUtil;
