import '@ant-design/compatible/assets/index.css';
import {
  Input,Spin,Form,Upload,Select,Table,Card,
  Row,Col,Checkbox,Button,Radio,AutoComplete,
} from 'antd';
import { connect } from 'react-redux';
import { useState, useEffect } from 'react';
import { Qtable, Qmessage, Qbtn } from 'common';
import { BatchListGenreal,BatchListCross, ColumnsAddGeneral,ColumnsAddCross } from './columns';
import { GetOriginApi, GetEditApi } from 'api/home/BaseGoods';
import Creatlabel from './components/Creatlabel';
import EditableCell from './components/EditableCell';
import StandardsMod from './components/StandardsMod';
import ResetModal from './components/ResetModal';
import BaseInfoSet from './components/BaseInfoSet';
import './BaseGoodsAdd.less';

let FormItem = Form.Item;
let Option = Select.Option;
const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 4 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
    };
const formItemLayoutBig = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 4 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 20 },
      },
    };

const BaseGoodsAdd =({...props})=> {//productNature12
  const [form] = Form.useForm();
  let { specData, categoryData, goodsList, totalData } =props;
  let spuCode = props.match.params.id;
  let isEdit = spuCode?true:false;
  let productNature = props.match.params.type;
  let columnsAdd = productNature==1?ColumnsAddGeneral:ColumnsAddCross;
  let batchList = productNature==1?BatchListGenreal:BatchListCross;
  const [visible,setVisible] =useState(false)
  const [deletedList,setDeletedList] =useState([])
  //
  const initPage=()=> {
    if(spuCode) {
      props.dispatch({
        type:'baseGoodsAdd/fetchTotal',
        payload:{spuCode}
      })
    }
  }
  //
  const upDateGoodsList=(title,value)=> {
    goodsList = goodsList.map((el)=> {
      el = {...el,[title]:value};
      return el;
    });
    goodsList = [...goodsList]
    form.setFieldsValue({list:goodsList})
    props.dispatch({
      type:'baseGoodsAdd/getListState',
      payload:{goodsList}
    })
  }
  //
  const handleChangeLevel=(level,selected)=>{
    level++;
    props.dispatch({
      type:'baseGoodsAdd/fetchCategory',
      payload:{level,parentId:selected }
    })
  }
  //
  const goReturn=()=> {
    props.history.push('/account/items_list')
  }
  //
  const onOperateClick=(record)=> {
   goodsList = goodsList.filter(value => value.salesAttributeName !== record.salesAttributeName);
   props.dispatch({
     type:'baseGoodsAdd/getListState',
     payload:{ goodsList }
   })
  }
  //
  const goReset=()=> {
    let { specOne, specTwo } =specData;
    let newGoodsLIst = [...goodsList];
    if(specOne.length>0&&specTwo.length>0) {
      specOne.map((el)=> {
        specTwo.map((item) => {
          let lem = {...el,name:`${el.name}/${item.name}`};
          deletedList.push(lem);
        })
      })
    } else {
      specOne.map((el)=> {
        deletedList.push(el);
      })
    }
    newGoodsLIst.map((el,index)=> {
      deletedList.map((item,idx)=> {
        if(el.salesAttributeName == item.name) {
          deletedList.splice(idx,1);
        }
      });
    })
    if(deletedList.length==0) {
      Qmessage.error('SKU');
      return;
    }
    setDeletedList(deletedList);
    setVisible(true);
  }
  const onCancel=()=> {
    setDeletedList([]);
    setVisible(false);
  }
  //
  const onOk=(selectVal)=> {
    let resetList = selectVal.map((el)=> {
      let item ={};
      item.salesAttributeName=el;
      item.key=el;
      return item;
    })
    goodsList = [...goodsList,...resetList];
    props.dispatch({
      type:'baseGoodsAdd/getListState',
      payload:{ goodsList }
    })
    onCancel();
  }
  //
  const submit = async (saveType) => {
    try {
      const values = await form.validateFields();
      let { brandName, country, categoryId, categoryId2, categoryId3, categoryId4, pdType1Id, pdType2Id, list, ...paramsVal} = values;
      list = list&&list.map((el,index)=> {
        goodsList.map((item,idx) => {
          if(index == idx) {
            el.salesAttributeName = item.salesAttributeName;
            el.skuCode = item.skuCode;
          }
        })
        return el;
      })
      let listOne = specData.specOne.map((el)=> el.name);
      let listTwo = specData.specTwo.map((el)=> el.name);
      paramsVal = {
        ...paramsVal,
        country:totalData.countryCode,
        brandId:totalData.brandId,
        categoryId:categoryId4,
        productNature,
        saveType,
        list,
        attrList: [{
          attributeId:pdType1Id,
          attributeValueList:listOne
        },{
          attributeId:pdType2Id,
          attributeValueList:listTwo
        }]
      }
      if(spuCode) { paramsVal = {...paramsVal,spuCode} }
      GetEditApi(paramsVal)
      .then((res)=> {
        Qmessage.success('');
        goReturn();
      })
    } catch (errorInfo) {
      console.log('Failed:', errorInfo);
    }
  }
  //change
  const onValuesChange=(changedValues, allValues)=> {
    let currentKey = Object.keys(changedValues)[0];
    let { sendType, list } = changedValues;
    if(currentKey!='list') {
      if(sendType == 2) {
        changedValues['isBeforeSales'] =true;
      }
      props.dispatch({
        type:'baseGoodsAdd/getTotalState',
        payload:changedValues
      })
    } else if(currentKey=='list') {
      goodsList = goodsList.map((el,index)=> {
        list.map((item,idx)=> {
          if(index == idx) {
            el = {...el,...item}
          }
        })
        return el;
      })
      props.dispatch({
        type:'baseGoodsAdd/getListState',
        payload:{ goodsList }
      })
    }
  }
  useEffect(()=>{
    initPage();
    return () => {
      props.dispatch({
        type:'baseGoodsAdd/resetPage',
        payload:{}
      })
    };
  },[spuCode])
  useEffect(()=>{ form.setFieldsValue(totalData) },[totalData])
  useEffect(()=>{ form.setFieldsValue({list:goodsList}) },[goodsList]);

  return (
    <Spin tip="..." spinning={props.loading}>
      <div className="oms-common-addEdit-pages baseGoods-addEdit-pages">
        <Form
          onValuesChange={onValuesChange}
          className="common-addEdit-form"
          form={form}
          {...formItemLayout}>
          <BaseInfoSet
            {...props}
            isEdit={isEdit}
            form={form}
            productNature={productNature}/>
          {
            productNature==1&&
            <div>
              <Card title="">
                <Form.Item label="" name="profits" rules={[{ required: true, message: '' }]}>
                  <Radio.Group>
                    <Radio value={2} key={2}></Radio>
                    <Radio value={1} key={1}></Radio>
                  </Radio.Group>
                </Form.Item>
                <Form.Item
                label="B"
                name="minBoxSpecification"
                rules={[
                  { required: true, message: '1' },
                  { pattern:/^\d+(\.\d{1,2})?$/,message:'' },
                ]}>
                  <Input placeholder="1" autoComplete="off"/>
                </Form.Item>
                <Form.Item label="" name="sendType" rules={[{ required: true, message: '' }]}>
                  <Radio.Group>
                    <Radio value={2} key={2}></Radio>
                    <Radio value={1} key={1}></Radio>
                  </Radio.Group>
                </Form.Item>
                <Form.Item
                  noStyle
                  shouldUpdate={(prevValues, currentValues) => prevValues.sendType !== currentValues.sendType}>
                  {({ getFieldValue }) => {
                    return getFieldValue('sendType') == 2&&
                    <Form.Item  label="">
                      <Form.Item
                        noStyle
                        name="distributionDays"
                        rules={[
                          { required: true, message: '0' },
                          { pattern:/^\d+(\.\d{1,2})?$/,message:'' },
                        ]}>
                          <Input placeholder="0" className="short-input" autoComplete="off"/>
                      </Form.Item>
                      <span></span>
                    </Form.Item>
                  }}
                </Form.Item>
                <Form.Item label="" name="isBeforeSales" rules={[{ required: true, message: '' }]}>
                  <Radio.Group>
                    <Radio value={true} key={true}></Radio>
                    <Radio value={false} key={false} disabled={totalData.sendType==2?true:false}></Radio>
                  </Radio.Group>
                </Form.Item>
                <Form.Item label="" name="isDirectSales" rules={[{ required: true, message: '' }]}>
                  <Radio.Group>
                    <Radio value={true} key={true}></Radio>
                    <Radio value={false} key={false}></Radio>
                  </Radio.Group>
                </Form.Item>
              </Card>
              <Card title="">
                <Form.Item
                  label=""
                  name="basicsBoxSpecification"
                  rules={[
                    { required: true, message: '1' },
                    { pattern:/^\d+(\.\d{1,2})?$/,message:'' },
                  ]}>
                  <Input placeholder="1" autoComplete="off"/>
                </Form.Item>
                <Form.Item label="" name="batchProcessingStatus" rules={[{ required: true, message: '' }]}>
                  <Radio.Group>
                    <Radio value={2} key={2}></Radio>
                    <Radio value={1} key={1}></Radio>
                  </Radio.Group>
                </Form.Item>
                <Form.Item
                  noStyle
                  shouldUpdate={(prevValues, currentValues) => prevValues.batchProcessingStatus !== currentValues.batchProcessingStatus}>
                  {({ getFieldValue }) => {
                    return getFieldValue('batchProcessingStatus') == 2&&
                    <div>
                      <Form.Item label="" name="batchProcessingType" rules={[{ required: true, message: '' }]}>
                        <Radio.Group>
                          <Radio value={1} key={1}></Radio>
                          <Radio value={2} key={2}></Radio>
                        </Radio.Group>
                      </Form.Item>
                      <Form.Item label="" name="lotLimitInDay" rules={[
                        { required: true, message: '0' },
                        { pattern:/^\d+(\.\d{1,2})?$/,message:'' },
                      ]}>
                        <Input placeholder="0" autoComplete="off"/>
                      </Form.Item>
                    </div>
                  }}
                </Form.Item>
              </Card>
            </div>
          }
          <Card title="SKU">
            <StandardsMod {...props}/>
            <Form.Item label="" {...formItemLayoutBig} className="table-wrap-fomItem">
              <Qtable
                scroll={{x:'100%'}}
                dataSource={goodsList}
                columns={columnsAdd}
                onOperateClick={onOperateClick}/>
            </Form.Item>
            <Row >
              <Col offset={4}>
                <Qbtn size="free" onClick={goReset}>SKU</Qbtn>
              </Col>
            </Row>
            <Form.Item label="">
              <div style={{display:'flex',textAlign:'center'}}>
                {
                  batchList.map((el,index) => (
                    <EditableCell key={index} text={el.value} upDateList={(value)=>upDateGoodsList(el.key,value)}/>
                  ))
                }
              </div>
            </Form.Item>
          </Card>
          <div className="handle-operate-save-action">
            <Qbtn onClick={goReturn}>  </Qbtn>
            {
              totalData.isSave&&<Qbtn onClick={()=>submit(0)}></Qbtn>
            }
            <Qbtn size="free" onClick={()=>submit(1)}></Qbtn>
          </div>
        </Form>
      </div>
      <ResetModal
        options={deletedList}
        visible={visible}
        onOk={onOk}
        onCancel={onCancel}/>
    </Spin>
  );
}
function mapStateToProps(state) {
  const { BaseGoodsAddReducers } =state;
  return BaseGoodsAddReducers;
}

export default connect(mapStateToProps)(BaseGoodsAdd);
