import React, { Component } from "react";
import {Spin} from 'antd'
import { Qtable, Qpagination,Qbtn  } from "common"; //
import FilterForm from "./components/FilterForm/index";
import Columns from "./columns";
import { saveStoreApi,getListApi  } from "api/home/StockCenter/StoreManage";
import AddStoreModal from "./components/AddStoreModal";
/**
 * zhy
 */
class CrossGoodStore extends Component {
  constructor(props) {
    super(props);
    this.state = {
      dataList: [],
      inputValues: { status: 10, sourceType: 2 },
      everyPage: 0,
      currentPage: 0,
      total: 0,
      loading: false,
      visible: false,
      id:''
    };
  }
  componentWillMount() {
    this.searchData({});
  }
  //
  searchData = values => {
    this.setState({
      loading: true
    });
    getListApi(values)
      .then(res => {
        this.setState({
          loading: false
        });
        if (res.httpCode == 200) {
          const { result, everyPage, currentPage, total } = res.result;
          if (result.length) {
            result.map(item => {
              item.key = item.id;
            });
          }
          this.setState({
            dataList: result,
            everyPage,
            currentPage,
            total
          });
        }
      })
      .catch(() => {
        this.setState({
          loading: false
        });
      });
    this.setState({ inputValues: values });
  };

  //
  changePage = (currentPage, everyPage) => {
    const values = { ...this.state.inputValues, currentPage, everyPage };
    this.searchData(values);
  };
  //
  addStore = () => {
    this.setState({
      visible: true
    });
  };
   //
   onOk = (values, clearForm) => {
    saveStoreApi(values).then(res => {
      if (res.httpCode == 200) {
        this.setState({
          visible: false,
          id: ""
        });
        clearForm();
        this.searchData({})
      }
    });
  };
  //
  onCancel = clearForm => {
    this.setState(
      {
        visible: false,
        id: ""
      },
      () => {
        clearForm();
      }
    );
  };
  handleOperateClick=(record)=>{
    this.setState({
      id:record.id,
      visible:true
    })
  }
  render() {
    const { dataList, everyPage, currentPage, total, loading,visible,id } = this.state;
    return (
      <Spin spinning={loading}>
        <div className="oms-common-index-pages-wrap">
          <FilterForm onSubmit={this.searchData} />
          <div className="handle-operate-btn-action">
            <Qbtn onClick={this.addStore}></Qbtn>
          </div>
          <Qtable dataSource={dataList} columns={Columns} onOperateClick={this.handleOperateClick}/>
          {dataList.length > 0 ? (
            <Qpagination
              data={{ everyPage, currentPage, total }}
              onChange={this.changePage}
            />
          ) : null}
          {visible && (
            <AddStoreModal
              id={id}
              visible={visible}
              onOk={this.onOk}
              onCancel={this.onCancel}
            />
          )}
        </div>
      </Spin>
    );
  }
}

export default CrossGoodStore;
