import React, { Component } from "react";
import { Button, Modal, Form, Input, Select, message } from "antd";
const FormItem = Form.Item;
const Option = Select.Option;
const TextArea = Input.TextArea;

const InjectCoupon=({...props})=>{
  const onOk = async() => {
    try {
      let values = await props.form.validateFields(['userMobiles','resonance']);
      const { userMobiles } = values;
      let mobileArr = [];
      let isTrue = false;
      if (userMobiles.split("\n").length > 100) {
        this.props.setConfirmLoading(false);
        return message.error("100");
      }
      if (userMobiles.indexOf("\n") != -1) {
        mobileArr = userMobiles.split("\n");
        mobileArr.map((item, index) => {
          if (item.length > 11) {//11
            isTrue = true;
          }
          return item;
        });
      } else if (userMobiles.length > 11) {//
        isTrue = true;
      }
      if (isTrue) {
        this.props.setConfirmLoading(false);
        return message.error("", 0.8);
      }
      props.onOk(values)
    } catch (errorInfo) {
      console.log('Failed:', errorInfo);
    }
  };
  return (
      <Modal
        destroyOnClose={true}
        visible={props.visible}
        title=""
        okText=""
        onCancel={props.onCancel}
        onOk={onOk}>
          <FormItem
            label="">
            {props.record.couponName}
          </FormItem>
          <FormItem
            label=""
            name="userMobiles"
            rules={[{ required: true, message: "" }]}>
            <TextArea
              rows={10}
              placeholder="100"
              autoComplete="off"/>
          </FormItem>
          <FormItem
            label=""
            name="resonance"
            rules={[{ required: true, message: "" }]}>
            <Select allowClear={true} placeholder="">
              <Option value="1"></Option>
              <Option value="2"></Option>
              <Option value="3"></Option>
            </Select>
          </FormItem>
      </Modal>
  );
}
export default InjectCoupon;
