import { Table,Input,Form } from 'antd';


class EditTable extends React.Component {
  renderSkuTips=(text,record,index)=> {
    return <Form.Item name={['subList',index,'skuTips']}>
               <Input autoComplete="off" placeholder="30C"/>
            </Form.Item>
  }
  renderSkuShelfLife=(text,record,index)=> {
    return <Form.Item name={['subList',index,'skuShelfLife']}>
               <Input autoComplete="off" placeholder=""/>
            </Form.Item>
  }
  render() {
    return(
      <Table
        dataSource={this.props.dataSource}
        rowKey={(record) =>record.key}
        pagination={false}
        bordered={true}>
          <Table.Column title="sku" dataIndex="skuCode"/>
          <Table.Column title="" dataIndex ='salesAttributeName'/>
          <Table.Column title="" dataIndex ='barCode'/>
          <Table.Column title="C" dataIndex ='customerPrice'/>
          <Table.Column title="sku" dataIndex ='image' />
          <Table.Column title="" dataIndex ='skuTips' render={this.renderSkuTips}/>
          <Table.Column title="" dataIndex ='skuShelfLife' render={this.renderSkuShelfLife}/>
      </Table>
    )
  }
}

export default EditTable;
