import React, { useEffect, useState } from "react";
import { getInfosApi } from "api/home/OperateCenter/Boperate/Bpush";//
import './index.less'

/**
 * 
 * 
 * B
 * 
 */
const BpushDetail = props => {
  const [infos, setInfos] = useState({});
  useEffect(() => {
    const { id } = props.match.params;
    getInfosApi(id).then(res => {
      if (res.httpCode == 200) {
        setInfos(res.result);
      }
    });
  },[]);
  return (
    <div className="bpush_detail">
      <p className="tail">{infos.title}</p>
      <p className="tail">{infos.pushTime}</p>
      <p className="tail">{infos.msgContent}</p>
      <p className="tail">{infos.alertTypeStr}</p>
      <p className="tail">{infos.alertTypeContent}</p>
      <p className="tail">{infos.pushMan}</p>
    </div>
  );
};
export default BpushDetail;
