import React, { Component } from "react";
import { Link } from "react-router-dom";
import { Button, message, Menu, Dropdown,Modal } from "antd";
import {
  GetTaskListsApi,
  goInvalidApi
} from "api/home/GoodsCenter/Cgoods/Ctask";
import Columns from "./columns/index";
import Qtable from "common/Qtable/index"; //
import Qpagination from "common/Qpagination/index"; //
import FilterForm from "./FilterForm/index";
import { Sessions } from 'utils';
import "./index.less";

class Ctask extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: false,
      taskList: [],
      taskId: "",
      taskName: "",
      everyPage: 20,
      currentPage: 1,
      totalCount: 0,
      inputValues: {}
    };
  }

  //
  componentDidMount = () => {
    this.searchData({});
  };
  //
  searchData = values => {
    const params = { ...this.state.inputValues, ...values};
    GetTaskListsApi(params).then(res => {
      if (res.httpCode == 200) {
        let { result, everyPage, currentPage, totalCount } = res.result;
        this.setState({
          taskList:result,
          everyPage,
          currentPage,
          totalCount
        });
      }
    });
    this.setState({ inputValues: params });
  };
  //
  changePage = (currentPage, everyPage) => {
    const params = { currentPage, everyPage };
    this.searchData(params);
  };
  //
  onSubmit = params => {
    this.searchData(params);
  };
  //
  handleOperateClick = (record, type) => {
    switch (type) {
      case "info":
        this.props.history.push(`/account/taskInfo/${record.taskId}`);
        break;
      case "edit":
        this.props.history.push(
          `/account/addtask?taskType=1&taskId=${record.taskId}`
        );
        break;
      case "invalid":
        this.confirmInvalid(record);
    }
  };
  confirmInvalid = record => {
    this.setState({
      visible: true,
      taskId: record.taskId,
      taskName: record.taskName
    });
  };
  //
  onOk = () => {
    const userName = Sessions.get('oms_userName')||1234;
    const { taskId } = this.state;
    goInvalidApi({ taskId,taskOperateUser: userName }).then(res => {
      if (res.httpCode == 200) {
        message.success("", 0.8);
      }
      this.setState({
        visible: false,
        taskId: "",
        taskName: ""
      });
    });
  };
  //
  onCancel = () => {
    this.setState({
      visible: false,
      taskId: "",
      taskName: ""
    });
  };
  render() {
    const {
      taskList,
      everyPage,
      currentPage,
      totalCount,
      visible,
      taskName
    } = this.state;
    taskList.map(item=>(
      item.key = item.taskId
    ))
    return (
      <div className="oms-common-index-pages-wrap">
        <FilterForm onSubmit={this.onSubmit} />
        <div className="handle-operate-btn-action">
          <Dropdown
            overlay={
              <Menu>
                <Menu.Item>
                  <Link
                    to={"/account/addtask?taskType=1"}
                    style={{ color: "#35bab0" }}
                  >
                    
                  </Link>
                </Menu.Item>
                {/* <Menu.Item>
                  <Link
                    to={"/account/addtask?taskType=2"}
                    style={{ color: "#35bab0" }}
                  >
                    
                  </Link>
                </Menu.Item>
                <Menu.Item>
                  <Link
                    to={"/account/addtask?taskType=3"}
                    style={{ color: "#35bab0" }}
                  >
                    
                  </Link>
                </Menu.Item> */}
              </Menu>
            }
            placement="bottomCenter"
            overlayClassName="set-time"
          >
            <Button type="primary" size="large">
              
            </Button>
          </Dropdown>
        </div>
        <Qtable
          columns={Columns}
          dataSource={taskList}
          onOperateClick={this.handleOperateClick}
        />
        <Qpagination
          data={{ everyPage, currentPage, totalCount }}
          onChange={this.changePage}
        />
        {visible && (
          <Modal
            className='modal_center'
            title=""
            visible={visible}
            onOk={this.onOk}
            onCancel={this.onCancel}
          >
            <span>{taskName}</span>
          </Modal>
        )}
      </div>
    );
  }
}
export default Ctask;
