import { useState, useEffect } from "react";
import { Modal, Input, Select, message, Form } from "antd";
import { AddAtrApi, UpdataAtrApi } from "api/home/Attributions";
const FormItem = Form.Item;
const Option = Select.Option;

const AddAtr = props => {
  const [form] = Form.useForm();
  const [confirmLoading, setConfirmLoading] = useState(false);
  /**
   * 
   */
  useEffect(() => {
    form.setFieldsValue(props);
  }, []);
  /**
   * 
   */
  const clearForm = () => {
    form.resetFields();
  };
  /**
   * 
   */
  const onOk = async () => {
    const values = await form.validateFields();
    setConfirmLoading(true);
    const { attributeId } = props;
    if (attributeId) {
      //
      UpdataAtrApi({ id: attributeId, ...values })
        .then(res => {
          if (res.httpCode == 200) {
            message.success("", 0.8);
            props.onOk(clearForm);
          }
          setConfirmLoading(false);
        })
        .catch(error => {
          setConfirmLoading(false);
        });
    } else {
      //
      AddAtrApi(values)
        .then(res => {
          if (res.httpCode == 200) {
            message.success("", 0.8);
            props.onOk(clearForm);
          }
          setConfirmLoading(false);
        })
        .catch(error => {
          setConfirmLoading(false);
        });
    }
  };
  /**
   * 
   */
  const onCancel = () => {
    props.onCancel(clearForm);
  };
  const { attributeId, visible } = props;
  console.log(attributeId);
  return (
    <div>
      <Modal
        title={attributeId ? "" : ""}
        confirmLoading={confirmLoading}
        visible={visible}
        onOk={onOk}
        onCancel={onCancel}
        okText=""
        cancelText=""
      >
        <Form form={form}>
          <FormItem
            label=""
            name="attributeName"
            rules={[{ required: true, message: "" }]}
            labelCol={{ span: 5 }}
            wrapperCol={{ span: 12 }}
          >
            <Input placeholder=",5" maxLength={5} autoComplete="off" />
          </FormItem>
          <FormItem
            label=""
            name="attributeState"
            labelCol={{ span: 5 }}
            wrapperCol={{ span: 12 }}
            rules={[{ required: true, message: "" }]}
          >
            <Select
              allowClear={true}
              placeholder=""
              className="select"
            >
              <Option value={0}></Option>
              <Option value={1}></Option>
            </Select>
          </FormItem>
        </Form>
      </Modal>
    </div>
  );
};
export default AddAtr;
