import React from "react";
import { Form, Input, Select, Row, Col } from "antd";
import { GetCategoryApi } from "api/home/BaseGoods";
import { BaseFilter, Qbtn } from "common";
const FormItem = Form.Item;
const { Option } = Select;

class SearchForm extends BaseFilter {
  formRef = React.createRef();
  constructor(props) {
    super(props);
    this.state = {
      catagoryList: [],
      catagoryList2: []
    };
  }
  componentDidMount() {
    GetCategoryApi({ level: 1, parentId: "" }).then(res => {
      this.setState({ catagoryList: res.result || [] });
    });
  }
  //
  onChange = value => {
    this.formRef.current.setFieldsValue({ pdCategory2Id: undefined });
    this.setState({
      catagoryList2: []
    });
    if (value) {
      GetCategoryApi({ level: -1, parentId: value }).then(res => {
        this.setState({
          catagoryList2: res.result || []
        });
      });
    }
  };
  render() {
    const { catagoryList, catagoryList2 } = this.state;
    return (
      <div className="qtoolOms-condition">
        <Form
          className="serach-common-form"
          ref={this.formRef}
          {...this.formItemLayout}
        >
          <Row gutter={24}>
            <Col {...this.colspans}>
              <FormItem name="productName" label="">
                <Input placeholder="" autoComplete="off" />
              </FormItem>
            </Col>
            <Col {...this.colspans}>
              <FormItem name="skuCode" label="sku">
                <Input placeholder="sku" autoComplete="off" />
              </FormItem>
            </Col>
            <Col {...this.colspans}>
              <FormItem name="spuCode" label="SPU">
                <Input placeholder="SPU" autoComplete="off" />
              </FormItem>
            </Col>
            <Col {...this.colspans}>
              <FormItem name="productType" label="">
                <Select placeholder="" allowClear={true}>
                  <Option value={1}></Option>
                  <Option value={2}></Option>
                </Select>
              </FormItem>
            </Col>
            <Col {...this.colspans}>
              <FormItem name="pdCategory1Id" label="">
                <Select
                  onChange={this.onChange}
                  placeholder=""
                  allowClear={true}
                >
                  {catagoryList.map(item => (
                    <Option value={item.id} key={item.id}>
                      {item.categoryName}
                    </Option>
                  ))}
                </Select>
              </FormItem>
            </Col>
            <Col {...this.colspans}>
              <FormItem name="pdCategory2Id" label="">
                <Select
                  placeholder=""
                  disabled={!catagoryList2.length > 0}
                  allowClear={true}
                >
                  {catagoryList2.map(item => (
                    <Option value={item.id} key={item.id}>
                      {item.categoryName}
                    </Option>
                  ))}
                </Select>
              </FormItem>
            </Col>
          </Row>
        </Form>
        <Col span={24}>
          <FormItem className="oms-condition-operate">
            <Qbtn type="primary" onClick={this.handleSubmit}>
              
            </Qbtn>
          </FormItem>
        </Col>
      </div>
    );
  }
}
export default SearchForm;
