const ColumnsAdd =(onBlur, validatePrice)=> {
  return [
    {
      title: '',
      key:'pdCode',
      render:(text,record,index)=>{
        return(
          <FormItem name={['productList',index,'skuCode']} rules={[{required:true,message:''}]}>
            <Input
              placeholder=''
              onBlur={(e)=>onBlur(e,index)}
              autoComplete='off'/>
          </FormItem>
        )
      }
    },{
      title: '',
      key:'name',
      dataIndex: 'productName',
    },{
      title: '',
      dataIndex: 'salesAttributeName',
    },{
      title: '',
      dataIndex: 'businessPrice',
    },{
      title: '',
      key:'activitySupplyPrice',
      dataIndex: 'activitySupplyPrice',
      render:(text,record,index)=>{
        const validatePrice=(rule,value)=> {
          if(value){
            if(Number(value) > Number( record.businessPrice ) || Number(value) == Number( record.businessPrice ) ){
                return Promise.reject('');
            };
          };
          return Promise.resolve();
        };
        return(
          <FormItem
            name={['productList',index,'activitySupplyPrice']}
            rules={[
              {required:true,message:''},
              {pattern:/^[1-9]\d*\.\d{1,2}$|0\.[1-9]\d{0,1}$|0\.0[1-9]{1}$|^[1-9]\d*$/,message:'02'},
              {validator:validatePrice}
            ]}>
            <Input placeholder='' autoComplete='off'/>
          </FormItem>
        )
      }
    }];
}
