import React, { useState } from "react";
import { Upload, Button, Modal, Table, message } from "antd";
import "./index.less";

let keyIndex = 100;
/**
 *                                  
 * @param {Columns:[]}        true
 * @param {dataSource:[]}     true
 * @param {action:String} url   true
 * @param {data:{}} post
 * @param {del:Boolean}  false
 * @param {footer:Boolean}  false
 * @param {changeDataSource:function}  true
 *
 */

const UpLoadFile = ({
  Columns,
  dataSource = [],
  action,
  data,
  del,
  footer,
  changeDataSource,
  downLoadTemp
}) => {
  const [errorMsg, setErrMsg] = useState("");
  const [visible, setVisible] = useState("");
  /**
   *change
   * @param {*} info
   */
  const handleChange = info => {
    let file = info.file;
    if (file.status == "done") {
      if (file.response && file.response.httpCode == "200") {
        const { result } = file.response;
        if (result.constructor == Object) {
          if (result.hasOwnProperty("msg")) {
            if (result['msg']&&result['msg']!='success') {
              setErrMsg(result['msg']);
              setVisible(true);
            }
            result.result.map((item, index) => {
              item.key = index;
            });
            changeDataSource(result["result"]);
            return;
          }
          if (result["message"]) {
            setErrMsg(result["message"]);
            setVisible(true);
          }
          result.list.map((item, index) => {
            item.key = index;
          });
          changeDataSource(result["list"]);
          return;
        }
        result.map((item, index) => {
          item.key = index;
        });
        changeDataSource(result);
      } else {
        message.error(file.response.msg, 0.8);
      }
    }
  };
  /**
   * 
   */
  const downLoadTempFuc = () => {
    downLoadTemp();
  };
  /**
   * 
   */
  const onCancel = () => {
    setVisible(false);
  };
  /**
   * 
   */
  const add = () => {
    keyIndex++;
    const newData = [...dataSource, { key: keyIndex }];
    changeDataSource(newData);
  };
  /**
   * 
   * @param {*} record
   */
  const delet = record => {
    let newData = [...dataSource];
    const index = newData.findIndex(item => record.key == item.key);
    newData.splice(index, 1);
    changeDataSource(newData);
  };
/**
 * Columns--->
 */
  const EditColumns = del
    ? [
        ...Columns,
        {
          title: "",
          render: (text, record, index) => {
            return (
              dataSource.length > 1 && <a onClick={() => delet(record)}></a>
            );
          }
        }
      ]
    : Columns;
/**
 * 
 */
  let params = null;
  if (data) {
    params = JSON.stringify(data);
  }
  /**
   * upload  
   */
  const Props = {
    accept: ".xlsx,.xls",
    name: "mfile",
    action: action,
    onChange: handleChange,
    data: { data: params },
    showUploadList: false
  };

  return (
    <div>
      <div className="add_task_upload">
        <Upload {...Props}>
          <Button type="primary" size="large">
            
          </Button>
        </Upload>
        <a className="download" onClick={downLoadTempFuc}>
          
        </a>
      </div>
      {dataSource.length > 0 && (
        <Table
          dataSource={dataSource}
          columns={EditColumns}
          pagination={false}
          bordered={true}
          rowKey={record => record.key}
          footer={footer ? () => <Button onClick={add}>+</Button> : null}
        />
      )}
      {visible && (
        <Modal
          title=""
          visible={visible}
          footer={null}
          onCancel={onCancel}
        >
          <div>
            <p style={{ color: "#35bab0" }}>
              {dataSource.length}
            </p>
            {errorMsg && <p>{errorMsg}</p>}
          </div>
        </Modal>
      )}
    </div>
  );
};
export default UpLoadFile;
