import React, { useState,useEffect } from "react";
import { Card, Form } from "antd";
import { Qtable } from "common";
import {
  GoodColumns,
  SubOrderColumns,
  ExpressColumns,
  OrderLogsColumns,
  HangzhouOrderLogsColumns,
  HangzhouClearLogsColumns
} from "./columns";
import { getBonedInfoApi } from "api/home/OrderCenter/Corder/UserOrder";

/**
 * 
 * @param {*} props 
 */
const BondedOrderInfo = props => {
  const [orderInfo, setOrderInfo] = useState({});
  const [receiveInfo, setReceiveInfo] = useState({});
  const [skuList, setSkuList] = useState([]);
  const [packageList, setPackageList] = useState([]);
  const [expressInfo, setExpressInfo] = useState([]);
  const [orderOperateLogList, setOrderOperateLogList] = useState([]);
  const [otherOperateLogList, setOtherOperateLogList] = useState([]);
  const {id} = props.match.params;
  useEffect(() => {
    getBonedInfoApi({ orderNo: id }).then(res => {
      if (res.httpCode == 200) {
        const {
          orderInfo,
          receiveInfo,
          skuList,
          packageList,
          expressInfo,
          orderOperateLogList,
          otherOperateLogList
        } = res.result;
        setOrderInfo(orderInfo);
        setReceiveInfo(receiveInfo);
        setSkuList(skuList);
        setPackageList(packageList);
        setExpressInfo(expressInfo);
        setOrderOperateLogList(orderOperateLogList);
        setOtherOperateLogList(otherOperateLogList);
      }
    });
  }, []);
  return (
    <div>
      <Card title="" className="base_info">
        <Form.Item label="">{orderInfo.orderNo}</Form.Item>
        <Form.Item label="">{orderInfo.createTimeStr}</Form.Item>
        <Form.Item label="">{orderInfo.orderStatusStr}</Form.Item>
        <Form.Item label="">{orderInfo.deliveryTypeStr}</Form.Item>
        <Form.Item label="">{orderInfo.orderAmount}</Form.Item>
        <Form.Item label="">{orderInfo.discountAmount}</Form.Item>
        <Form.Item label="">{orderInfo.couponAmount}</Form.Item>
        <Form.Item label="">{orderInfo.couponCode}</Form.Item>
        <Form.Item label="">{orderInfo.appVersion}</Form.Item>
      </Card>

      <Card title="" className="base_info">
        <Form.Item label="">{receiveInfo.nickname}</Form.Item>
        <Form.Item label="">{receiveInfo.idCardFrontPic}</Form.Item>
        <Form.Item label="">{receiveInfo.receiveUserName}</Form.Item>
        <Form.Item label="">{receiveInfo.receiveUserMobile}</Form.Item>
        <Form.Item label="">{receiveInfo.receiveUserMobile}</Form.Item>
      </Card>

      <Card title="">
        <Qtable columns={GoodColumns} dataSource={skuList} />
      </Card>

      {packageList.length > 0 &&
        packageList.map((item, index) => (
          <Card title={`${index + 1}`} className="base_info">
            <div>
              <div>
                <Form.Item label="">{orderInfo.appVersion}</Form.Item>
                <Form.Item label="">{orderInfo.appVersion}</Form.Item>
                <Form.Item label="">{orderInfo.appVersion}</Form.Item>
              </div>
              <Qtable columns={SubOrderColumns} dataSource={skuList} />
            </div>
          </Card>
        ))}

      <Card title="">
        <Qtable columns={ExpressColumns} dataSource={expressInfo} />
      </Card>

      <Card title="">
        <Qtable columns={OrderLogsColumns} dataSource={orderOperateLogList} />
      </Card>

      <Card title="">
        <Qtable columns={HangzhouOrderLogsColumns} dataSource={skuList} />
      </Card>

      {otherOperateLogList.length > 0 &&
        otherOperateLogList.map((item, index) => (
          <Card title={`${item.titleName}${index + 1}`}>
            <Qtable columns={HangzhouClearLogsColumns} dataSource={item.operateLogList} />
          </Card>
        ))}
    </div>
  );
};
export default BondedOrderInfo;
