import { Form, Checkbox, Col, Input, Radio, message, Button } from "antd";
import { useState, useEffect } from 'react';
import { QupLoadImgLimt, Qbtn, Qmessage } from 'common';
import { GetModalInfoApi } from 'api/contentCenter/BannerSetCtip';
import { GetSaveSetApi } from 'api/contentCenter/SingleGoodsSet';

const FormItem = Form.Item;

const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 4 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 20 },
      },
    };

const ModuleSet=({...props})=> {
  const [form] = Form.useForm();
  let [totalData,setTotalData]=useState([]);
  let homepageModuleId = props.match.params.id;
  const initPage = () => {
    GetModalInfoApi(homepageModuleId)
    .then(res => {
      setTotalData(res.result)
    });
  };
  const handleSubmit =async()=> {
    try {
      let  values = await form.validateFields();
      GetSaveSetApi({...values, homepageModuleId})
      .then(res => {
        Qmessage.success('')
      })
    } catch (errorInfo) {
      console.log('Failed:', errorInfo);
    }
  }
  useEffect(()=>{ form.setFieldsValue(totalData) },[totalData])
  useEffect(()=>{ initPage() },[homepageModuleId])
  return (
    <div className="oms-common-addEdit-pages">
      <Form form={form} {...formItemLayout} className="common-addEdit-form">
        <FormItem label="">
          <FormItem name="title" rules={[{ required: true, message: '' } ]} noStyle>
            <Input  placeholder="" autoComplete="off" maxLength={4}/>
          </FormItem>
          CApp
        </FormItem>
        <FormItem label="" name="titleColor" rules={[{ required: true, message: '' } ]}>
          <Radio.Group>
            <Radio value={'0'}></Radio>
            <Radio value={'1'}></Radio>
          </Radio.Group>
        </FormItem>
        <FormItem label="" name="isDisplayMore" rules={[{ required: true, message: '' } ]}>
          <Radio.Group>
            <Radio value={0}></Radio>
            <Radio value={1}></Radio>
          </Radio.Group>
        </FormItem>
        <FormItem label="" name="isDisplaySplitLine" rules={[{ required: true, message: '' } ]}>
          <Radio.Group>
            <Radio value={1}></Radio>
            <Radio value={0}></Radio>
          </Radio.Group>
        </FormItem>
        <FormItem label="">
          <FormItem name="isDisplayCountdown" noStyle>
            <Radio.Group>
              <Radio value={1}></Radio>
              <Radio value={0}></Radio>
            </Radio.Group>
          </FormItem>
          -
        </FormItem>
        <FormItem label="" name="moduleBackColor">
          <Input type='color' style={{ width: "60px",height:"32px" }}/>
        </FormItem>
        <Col offset={4}>
          <Qbtn onClick={handleSubmit}> </Qbtn>
        </Col>
      </Form>
    </div>
  );
}
export default ModuleSet;
