import React, { useEffect, useState } from "react";
import { Modal, Form, Select, Button } from "antd";
import { QenlargeImg } from "common";
import { getInfosApi, auditRecharge } from "api/home/FinancialCenter/Recharge";
import {deBounce} from 'utils/tools'
import '../index.less'
const Option = Select.Option;
const FormItem = Form.Item;

const AuditRechargeModal = (props) => {
  const [form] = Form.useForm();
  const { spVoucherId, visible } = props;
  console.log(props);
  const [infos, setInfos] = useState({});
  const [repeatNos, setRepeatNos] = useState([]);
  const [imgs, setImgs] = useState([]);
  const [checkStatus, setCheckStatus] = useState(false);
  //
  useEffect(() => {
    getInfosApi(spVoucherId).then((res) => {
      if (res.httpCode == 200) {
        const { spVoucherDto, repeatNos, spVoucherDetails } = res.result;
        setImgs(spVoucherDetails);
        setInfos(spVoucherDto);
        setRepeatNos(repeatNos);
      }
    });
  }, []);
  const clearForm = () => {
    form.resetFields();
  };
  //
  const handlePass = deBounce(async (status) => {
    if (status == 2) {
      setCheckStatus(true);
    }
    const values = await form.validateFields();
    values.spVoucherId = spVoucherId;
    values.status = status;
    auditRecharge(values)
      .then((res) => {
        if (res.httpCode == 200) {
          props.onClear(clearForm);
        }
      })
  },500);
  return (
    <div>
      <Modal
        className='recharge_modal'
        visible={visible}
        title=""
        footer={[
          <Button key="back" onClick={() => handlePass(2)}>
            
          </Button>,
          <Button key="submit" type="primary" onClick={() => handlePass(1)}>
            
          </Button>,
        ]}
        onCancel={() => props.onClear(clearForm)}
      >
        <Form form={form}>
          <FormItem
            label=""
            labelCol={{ span: 5 }}
            wrapperCol={{ span: 12 }}
          >
            {infos.shopName}
          </FormItem>
          <FormItem
            label=""
            labelCol={{ span: 5 }}
            wrapperCol={{ span: 12 }}
          >
            {infos.amount}
          </FormItem>
          <FormItem
            label=""
            labelCol={{ span: 5 }}
            wrapperCol={{ span: 12 }}
          >
            {repeatNos.length ? (
              <p
                style={{
                  color: "red",
                  lineHeight: "16px",
                  marginBottom: 5,
                  marginTop: 5,
                }}
              >
                
                {repeatNos.join("")}
                
              </p>
            ) : (
              <div className="cz_imgboxsLists">
                {imgs &&
                  imgs.length &&
                  imgs.map((item, index) => (
                    <QenlargeImg
                      url={
                        sessionStorage.getItem("oms_fileDomain") + item.picUrl
                      }
                    />
                  ))}
              </div>
            )}
          </FormItem>
          <FormItem
            label=""
            labelCol={{ span: 5 }}
            wrapperCol={{ span: 12 }}
            name="remark"
            rules={[{ required: checkStatus, message: "" }]}
          >
            <Select placeholder="">
              <Option value=""></Option>
              <Option value=""></Option>
              <Option value=""></Option>
              <Option value=""></Option>
              <Option value=""></Option>
              <Option value=""></Option>
            </Select>
          </FormItem>
        </Form>
      </Modal>
    </div>
  );
};
export default AuditRechargeModal;
