import React, {useEffect, useState} from "react";
import {Card} from "antd";
import {QbaseInfo, Qtable} from "common/index";
import moment from "moment";
import {OrderLogsColumns, GoodsColumns} from "./column";
import { GetInfoApi } from "api/home/OrderCenter/Border/ShopReturnOrder";

const ShopReturnOrderDetail = (props) => {
    const [orderLogs, setOrderLogs] = useState([]);
    const [goodsList, setGoodsList] = useState([]);
    const [dataInfo, setDataInfo] = useState({});
    const {id} = props.match.params;
    const getInfo=()=> {
      GetInfoApi({reOrderNo:id})
      .then((res)=> {
        let { detailList, ...values} =res.result;
        detailList=detailList?detailList:[];
        detailList.map((el,index)=>el.key=index);
        setDataInfo(values)
        setGoodsList(detailList)
      })
    }
    useEffect(() => { getInfo() }, []);
    return (
        <div className="oms-common-addEdit-pages bgood_add">
            <Card title="">
                <QbaseInfo dataInfo={
                    [
                      {key:"", value:dataInfo.reOrderNo},
                      {key:"", value:dataInfo.channelOrderNo},
                      {key:"", value:dataInfo.channelName},
                      {key:"", value:dataInfo.reason},
                      {key:"", value:dataInfo.statusStr},
                      {key:"", value:dataInfo.inventedStr},
                      {key:"", value:dataInfo.warehouseName},
                      {key:"", value:dataInfo.courierCompany},
                      {key:"", value:dataInfo.expressNo},
                      {key:"", value:dataInfo.returnQty},
                      {key:"", value:dataInfo.itemCount},
                      {key:"", value:dataInfo.reArriveAmount},
                      {key:"", value:dataInfo.itemCount},
                      {key:"", value:dataInfo.refundMoney},
                      {key:"", value:dataInfo.createBy},
                      {key:"", value:moment(dataInfo.createTime).format("YYYY-MM-DD HH:mm:ss")},
                      {key:"", value:dataInfo.remarkes},
                    ]
                }/>
            </Card>
            <Card title="">
                <Qtable columns={GoodsColumns} dataSource={goodsList}/>
            </Card>
            <Card title="">
                <Qtable columns={OrderLogsColumns} dataSource={orderLogs}/>
            </Card>
        </div>
    )
};
export default ShopReturnOrderDetail
