import React, { useEffect, useState } from "react";
import { getInfosApi } from "api/home/OperateCenter/Coperate/Cpush";//
import './index.less'

const CpushDetail = props => {
  const [infos, setInfos] = useState({});
  useEffect(() => {
    const { id } = props.match.params;
    getInfosApi(id).then(res => {
      if (res.httpCode == 200) {
        setInfos(res.result);
      }
    });
  },[]);
  return (
    <div className="cpush_detail">
      <p className="tail">{infos.title}</p>
      <p className="tail">{infos.pushTime}</p>
      <p className="tail">{infos.msgContent}</p>
      <p className="tail">{infos.alertTypeStr}</p>
      <p className="tail">{infos.pushContent}</p>
      <p className="tail">{infos.pushMan}</p>
    </div>
  );
};
export default CpushDetail;
