import React, { Component } from "react";
import { Form, Modal, Input } from "antd";
import "../index.less";
const FormItem = Form.Item;
class EditModal extends Component {
  handleCancel = () => {
    this.props.handleCancel();
  };
  handleOk = async() => {
    try {
      let values = await this.props.form.validateFields(['pdCode','max']);
      this.props.handleOk(values);
    } catch (errorInfo) {
      console.log('Failed:', errorInfo);
    }
  };

  render() {
    const { visible, editType, pdCode, max, record } = this.props;
    return (
      <div>
        <Modal
          destroyOnClose={true}
          width={600}
          title={editType == "edit" ? "" : ""}
          visible={visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}>
          <div>
            {record.pdCode?
              <FormItem
                labelCol={{ span: 7 }}
                wrapperCol={{ span: 16 }}
                label="">
                <span>{record.pdCode}</span>
              </FormItem>
              :
              <FormItem
                labelCol={{ span: 7 }}
                wrapperCol={{ span: 16 }}
                label=""
                name="pdCode"
                rules={[{ required: true, message: "" }]}>
                  <Input
                    autoComplete="off"
                    style={{ width: "120px" }}
                    placeholder=""/>
              </FormItem>
            }
            <FormItem
              labelCol={{ span: 7 }}
              wrapperCol={{ span: 16 }}
              label="">
              <FormItem name="max" noStyle>
                <Input autoComplete="off" style={{ width: "120px" }} placeholder=""/>
              </FormItem>
              <span className="suffix_tips">
                
              </span>
            </FormItem>
          </div>
        </Modal>
      </div>
    );
  }
}
export default EditModal;
