import react, { useEffect, useState } from "react";
import { getHotSaleList, getColdSaleList } from "api/home/DataCenter/BaseData/GoodsData";
import { Qtable } from "common";
const Columns = [
  { title: "", dataIndex: "rank" },
  { title: "", dataIndex: "goodsCode" },
  { title: "", dataIndex: "barcode" },
  { title: "", dataIndex: "goodsName" },
  { title: "", dataIndex: "goodsRule" },
  { title: "", dataIndex: "saleQty" },
  { title: "", dataIndex: "saleAmount" },
];
const Columns2 = [
  { title: "", dataIndex: "rank" },
  { title: "", dataIndex: "goodsCode" },
  { title: "", dataIndex: "barcode" },
  { title: "", dataIndex: "goodsName" },
  { title: "", dataIndex: "goodsRule" },
  { title: "10", dataIndex: "saleQty" },
  { title: "", dataIndex: "invQty" },
];
const GoodListTable = (props) => {
  console.log(props)
  const url = props.location.search.substr(0).split('&');
  const obj = {};
  url.map(item=>{
    obj[item.split('=')[0]]=item.split('=')[1]
  });
  const {id,type} = obj;
  const [goodList,setGoodList] = useState([])
  useEffect(() => {
    if (id == 1) {
      //
      getHotSaleList({type}).then((res) => {
        if (res.httpCode == 200) {
          setGoodList(res.result);
        }
      });
    }
    if (id == 2) {
      //
      getColdSaleList({type}).then((res) => {
        if (res.httpCode == 200) {
          setGoodList(res.result);
        }
      });
    }
  }, []);
  return <Qtable columns={id==1?Columns:Columns2} dataSource={goodList} />;
};
export default GoodListTable
