const columnsCreatInfo =(dataSource)=>{
  return [{
      title: '',
      dataIndex: 'budget',
      width:'20%',
      render:(text,record,index) => {
        let chldrnDom = <span> {record.budget?`${record.budget}`:''} </span>
        const obj = {
          children: chldrnDom,
          props: {},
        };
        if (index === 0) {
          obj.props.rowSpan = dataSource.length;
        } else {
          obj.props.rowSpan = 0;
        }
        return obj;
      }
    },{
      title: '',
      dataIndex: 'bearerStr',
      width:'10%',
    },{
      title: '*',
      dataIndex: 'proportion',
      width:'30%',
      render:(text,record,index) => {
        return <span>{record.proportion}%</span>
      }
    },{
      title: '',
      dataIndex: 'remark',
      width:'40%',
    },
  ]
}
export  {columnsCreatInfo};
