function InitColumns(promotionType) {
  let columns;
  switch(promotionType) {
    case 10:
     columns = columnsAdd1;
     break;
    case 11:
     columns = columnsAdd2;
     break;
    case 20:
    case 21:
    case 23:
     columns = columnsAdd3;
     break;
    case 22:
     columns = columnsAdd4;
     break;
  };
  return columns;
}
