import { Steps } from 'antd';
import './index.less';

const { Step } = Steps;

function StepMod({step=0}) {
  return <Steps current={step} className="steps-wrap">
            <Step status={step==0?'process':'wait'} title="" />
            <Step status={step==1?'process':'wait'} title="" />
          </Steps>
}
export default StepMod;
