import React, { useEffect,useState } from "react";
import { Card, Form } from "antd";
import { Qtable } from "common";
import { ReturnGoods, ReturnLogs } from "./columns";
import { getInfoApi } from "api/home/OrderCenter/Corder/UserReturn/AllReturn";
import moment from "moment";

const AllReturnInfo = props => {
  
  const {id} = props.match.params;
  const [infos,setInfos] =useState({})
  const [detailList,setDetailList] =useState([])
  useEffect(() => {
    getInfoApi({reOrderNo:id}).then(res => {
      if (res.httpCode == 200) {
        setInfos(res.result);
        setDetailList( res.result.detailList)
      }
    });
  }, []);
  return (
    <div>
      <Card title="" className='base_info'>
        <Form.Item  label="">{infos.reOrderNo}</Form.Item >
        <Form.Item  label="">{infos.channelOrderNo}</Form.Item >
        <Form.Item  label="">{infos.deliveryTypeStr}</Form.Item >
        <Form.Item  label="">{infos.phone}</Form.Item >
        <Form.Item  label="">{infos.subject}</Form.Item >
        <Form.Item  label="">{infos.statusStr}</Form.Item >
        <Form.Item  label="">{infos.refundTypeStr}</Form.Item >
        <Form.Item  label="">{infos.inventedStr}</Form.Item >
        <Form.Item  label="">{infos.sync}</Form.Item >
        <Form.Item  label="">{infos.price}</Form.Item >
        <Form.Item  label="">{infos.refundMoney}</Form.Item >
        <Form.Item  label="">{infos.totalPrice}</Form.Item >
        <Form.Item  label="">
          {infos.createTime&&moment(infos.createTime).format("YYYY-MM-DD HH:mm:ss")}
        </Form.Item >
      </Card>
      <Card title="">
        <Qtable columns={ReturnGoods} dataSource={detailList} />
      </Card>
      <Card title="">
        <Form.Item label="">{infos.reason}</Form.Item>
        <Form.Item label="">{infos.remarkes}</Form.Item>
        <Form.Item label="">
          {infos.imgList &&
            infos.imgList.map(img => (
              <img src={localStorage.getInfoApi("oms_fileDomain") + img} />
            ))}
        </Form.Item>
      </Card>
      <Card title="">
        <Qtable columns={ReturnLogs} dataSource={detailList} />
      </Card>
    </div>
  );
};
export default AllReturnInfo;
