import React from "react";
import {Form, Input, Select, Row, Col} from "antd";
import {BaseFilter, Qbtn} from "common";
import moment from "moment";
import {
    SHOPKEEPER_COST_TYPE_ALLOCATE_SPENDING,
    SHOPKEEPER_COST_TYPE_CANCEL_THE_REFUND,
    SHOPKEEPER_COST_TYPE_CARRY_OUT,
    SHOPKEEPER_COST_TYPE_IN_ALLOCATING_INCOME,
    SHOPKEEPER_COST_TYPE_LOGISTICS,
    SHOPKEEPER_COST_TYPE_ORDER, SHOPKEEPER_COST_TYPE_SALES_OF_THE_REFUND,
    SHOPKEEPER_COST_TYPE_SALES_REVENUE,
    SHOPKEEPER_COST_TYPE_SETTLEMENT_OF_CASHIER,
    SHOPKEEPER_COST_TYPE_STORE_TOP_UP, SHOPKEEPER_COST_TYPE_WITHDRAWAL_RELEASE,
    SHOPKEEPER_STATUS_IN,
    SHOPKEEPER_STATUS_OUT,
} from "../../config";
import {FilterSearchRangeTime} from "common/QdisabledDateTime";

const FormItem = Form.Item;
const {Option} = Select;

export default class SearchForm extends BaseFilter {
    formRef = React.createRef();

    render() {
        return (
            <div className="qtoolOms-condition">
                <Form className="serach-common-form" ref={this.formRef}{...this.formItemLayout}>
                    <Row gutter={24}>
                        <Col {...this.colspans}>
                            <FormItem label="" name="shopName" {...this.formItemLayout}>
                                <Input placeholder="" autoComplete="off"/>
                            </FormItem>
                        </Col>
                        <Col {...this.colspans}>
                            <FormItem name="status" label="" {...this.formItemLayout}>
                                <Select placeholder="" allowClear={true}>
                                    <Option value={SHOPKEEPER_STATUS_OUT}></Option>
                                    <Option value={SHOPKEEPER_STATUS_IN}></Option>
                                </Select>
                            </FormItem>
                        </Col>
                        <Col {...this.colspans}>
                            <FormItem label="" name="type"   {...this.formItemLayout}>
                                <Select placeholder="" allowClear={true}>
                                    <Option value={SHOPKEEPER_COST_TYPE_ORDER}></Option>
                                    <Option value={SHOPKEEPER_COST_TYPE_STORE_TOP_UP}></Option>
                                    <Option value={SHOPKEEPER_COST_TYPE_LOGISTICS}></Option>
                                    <Option
                                        value={SHOPKEEPER_COST_TYPE_SETTLEMENT_OF_CASHIER}></Option>
                                    <Option
                                        value={SHOPKEEPER_COST_TYPE_CANCEL_THE_REFUND}></Option>
                                    <Option
                                        value={SHOPKEEPER_COST_TYPE_ALLOCATE_SPENDING}></Option>
                                    <Option
                                        value={SHOPKEEPER_COST_TYPE_IN_ALLOCATING_INCOME}></Option>
                                    <Option value={SHOPKEEPER_COST_TYPE_SALES_REVENUE}></Option>
                                    <Option value={SHOPKEEPER_COST_TYPE_CARRY_OUT}></Option>
                                    <Option
                                        value={SHOPKEEPER_COST_TYPE_WITHDRAWAL_RELEASE}></Option>
                                    <Option
                                        value={SHOPKEEPER_COST_TYPE_SALES_OF_THE_REFUND}></Option>
                                </Select>
                            </FormItem>
                        </Col>
                        <Col {...this.colspans}>
                            <FilterSearchRangeTime
                                selectTimeChange={this.props.selectTimeChange}
                                defaultValue={[moment(this.searchCriteriaDefaultStartTime), moment(this.searchCriteriaDefaultEndTime)]}
                                startTimeName="dateStart" endTimeName="dateEnd" label=""
                                itemLayout={this.formItemLayout}/>
                        </Col>
                    </Row>
                </Form>
                <Col offset={21}>
                    <FormItem className="oms-condition-operate">
                        <Qbtn type="primary" onClick={this.handleSubmit}>
                            
                        </Qbtn>
                    </FormItem>
                </Col>
            </div>
        );
    }
}
